import React , { useEffect ,useState} from "react";
import ExclusivityTableStyles from './ExclusivityTable.styles'
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import Zoom from '@material-ui/core/Zoom';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';


export default function ExclusivityTable(props) {
  const classes = ExclusivityTableStyles();

  const [productData , setProductData] = useState()
  const [exclusivityData , setExclusivityData] = useState()

  useEffect(() => {
		async function getData() {
			try {
           setProductData(props.products) 
           setExclusivityData(props.exclusivity)  
			} catch (e) {
				console.log(e)
			}
		}
		getData();
    }, [props])

  if(productData && exclusivityData){
  return (
    <Paper className={classes.Paper}>
    <Table className={classes.Table} >
                    <TableHead className={classes.TableHead}>
                      <TableRow className={classes.StyledHeaderTableRow} >
                      <TableCell className={classes.StyledHeaderTableCell} >Exclusivity</TableCell>
                        <TableCell className={classes.StyledHeaderTableCell} >Exclusivity Expiration Date</TableCell>
                        {Object.keys(productData).map((key,index) => {
                                return(
                                        <TableCell className={classes.StyledHeaderTableCellMapped}>Product {key}</TableCell>
                                )
                              }
                        )}
                      </TableRow>
                    </TableHead>
                    <TableBody className={classes.TableBody}>
                        {exclusivityData.map((item,index) => {	
                        return(
                          <TableRow className={classes.StyledTableRow} key="Marketing">
                            <Tooltip TransitionComponent={Zoom} title={item["exclusivity_definition"]}>
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                              {item["exclusivity_code"]}
                            </TableCell>
                            </Tooltip>
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                            {item["exclusivity_date"]}
                            </TableCell>
                            {Object.keys(productData).map((key,index) => {
                              return(
                              <TableCell className={classes.StyledTableCellMapped}>
                                {item["product_no"].includes(key)?
                                          <CheckCircleIcon className={classes.StyledTableCellCheck}/>
                                          :
                                          <div></div>
                                }
                                </TableCell>
                              )
                              
                              })}
                          </TableRow>
                        )
                        })}
                    </TableBody>
      </Table>
      </Paper>
    )
  }
  else{
    return(
      <div></div>
    )
  }
}