import React , {useState} from 'react';
import RangeFilterStyles from './RangeFilter.styles'
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import Collapse from "@material-ui/core/Collapse";
import Box from '@material-ui/core/Box';
import AddIcon from '@material-ui/icons/Add';

export default function RangeFilter(props) {
  const classes = RangeFilterStyles();
  const [filterExpanded, setFilterExpanded] = useState(false);
  


  const handleFilterChange = (value) => {
    props.handleChange(props.filterType, value);
  };

  return (
    <div className={classes.root}>
      <Box  display="flex" justifyContent="space-between"
      onClick={() => {
        if (filterExpanded) setFilterExpanded(false);
        else setFilterExpanded(true);
      }}
      style={{cursor:'pointer'}}>
      <Typography className = {classes.FilterTitle}
      aria-expanded={filterExpanded}
      >
          {props.title}
      </Typography>
      <AddIcon className = {classes.FilterTitleButton}/>
      </Box>
      <Collapse in={filterExpanded} timeout="auto" unmountOnExit>
          <Slider
            className = {classes.RangeSlider}
            value={props.selected}
            min = {props.options[0]}
            // max = {parseInt(new Date().getFullYear())}
            max = {props.options[1]}
            onChange = {(event,value)=>{
              handleFilterChange(value)}}
            valueLabelDisplay="auto"
            aria-labelledby="range-slider"
          />
      </Collapse>
    </div>
  );
}
