import React, { useState, useEffect } from 'react';
import FilterStyles from './Filters.styles';
import AutoComplete from './AutoCompleteComponent';
import RangeFilter from './RangeFilter';
import { UpdateRounded } from '@material-ui/icons';
import { HeadingTypography } from '../../CustomComponents/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';

export default function FiltersSur(props) {
  const classes = FilterStyles();
  const [dataRows, setDataRows] = useState(props.data);
  const [loading, setLoading] = useState(false);
  const [selectedCategory, setselectedCategory] = useState();

  const [filterList, setFilterList] = useState({
    appl_type: [],
    status_CMT: [],
    subpart_approval_description: [],
    Sponsor: [],
    product: [],
    // "center": []
  });

  const [filters, setFilters] = useState(props.filters);

  let tempFilterCounts = {
    appl_type: {},
    status_CMT: {},
    subpart_approval_description: {},
    Sponsor: {},
    product: {},
  };

  for (let i = 0; i < dataRows.length; i++) {
    if (dataRows[i]['appl_type'] in tempFilterCounts['appl_type']) {
      tempFilterCounts['appl_type'][dataRows[i]['appl_type']] =
        tempFilterCounts['appl_type'][dataRows[i]['appl_type']] + 1;
    } else {
      tempFilterCounts['appl_type'][dataRows[i]['appl_type']] = 1;
    }

    if (dataRows[i]['status_CMT'] in tempFilterCounts['status_CMT']) {
      tempFilterCounts['status_CMT'][dataRows[i]['status_CMT']] =
        tempFilterCounts['status_CMT'][dataRows[i]['status_CMT']] + 1;
    } else {
      tempFilterCounts['status_CMT'][dataRows[i]['status_CMT']] = 1;
    }

    if (
      dataRows[i]['subpart_approval_description'] in
      tempFilterCounts['subpart_approval_description']
    ) {
      tempFilterCounts['subpart_approval_description'][
        dataRows[i]['subpart_approval_description']
      ] =
        tempFilterCounts['subpart_approval_description'][
          dataRows[i]['subpart_approval_description']
        ] + 1;
    } else {
      tempFilterCounts['subpart_approval_description'][
        dataRows[i]['subpart_approval_description']
      ] = 1;
    }

    if (dataRows[i]['Sponsor'] in tempFilterCounts['Sponsor']) {
      tempFilterCounts['Sponsor'][dataRows[i]['Sponsor']] =
        tempFilterCounts['Sponsor'][dataRows[i]['Sponsor']] + 1;
    } else {
      tempFilterCounts['Sponsor'][dataRows[i]['Sponsor']] = 1;
    }

    if (dataRows[i]['product'] in tempFilterCounts['product']) {
      tempFilterCounts['product'][dataRows[i]['product']] =
        tempFilterCounts['product'][dataRows[i]['product']] + 1;
    } else {
      tempFilterCounts['product'][dataRows[i]['product']] = 1;
    }
  }

  const [filterCounts, setFilterCounts] = useState(tempFilterCounts);
  const [resetFlag, setResetFlag] = useState(false);
  const [categoryList, setcategoryList] = useState();

  useEffect(() => {
    async function getData() {
      try {
        if (props.filters && !resetFlag) {
          setFilters(props.filters);
        } else {
          setFilters({
            appl_type: [],
            status_CMT: [],
            subpart_approval_description: [],
            Sponsor: [],
            product: [],
            // "center": []
          });
        }
        let baseFilterList = { ...filterList };
        for (let i = 0; i < dataRows.length; i++) {
          if (!baseFilterList['appl_type'].includes(dataRows[i]['appl_type'])) {
            baseFilterList['appl_type'].push(dataRows[i]['appl_type']);
          }

          if (
            !baseFilterList['status_CMT'].includes(dataRows[i]['status_CMT'])
          ) {
            baseFilterList['status_CMT'].push(dataRows[i]['status_CMT']);
          }

          if (
            !baseFilterList['subpart_approval_description'].includes(
              dataRows[i]['subpart_approval_description']
            )
          ) {
            baseFilterList['subpart_approval_description'].push(
              dataRows[i]['subpart_approval_description']
            );
          }
          if (!baseFilterList['Sponsor'].includes(dataRows[i]['Sponsor'])) {
            baseFilterList['Sponsor'].push(dataRows[i]['Sponsor']);
          }
          if (!baseFilterList['product'].includes(dataRows[i]['product'])) {
            baseFilterList['product'].push(dataRows[i]['product']);
          }
          // if (!baseFilterList["center"].includes(dataRows[i]['CDER_OR_CBER']))
          // { baseFilterList["center"].push(dataRows[i]['CDER_OR_CBER']); }
        }

        for (let key in baseFilterList) {
          baseFilterList[key] = baseFilterList[key].filter(function (element) {
            return element !== undefined;
          });
        }

        setFilterList(baseFilterList);
        let updatedFilters = { ...filters };
        if (updatedFilters['appl_type'].length === 0)
          updatedFilters['appl_type'] = baseFilterList['appl_type'];
        if (updatedFilters['status_CMT'].length === 0)
          updatedFilters['status_CMT'] = baseFilterList['status_CMT'];
        if (updatedFilters['subpart_approval_description'].length === 0)
          updatedFilters['subpart_approval_description'] =
            baseFilterList['subpart_approval_description'];
        if (updatedFilters['Sponsor'].length === 0)
          updatedFilters['Sponsor'] = baseFilterList['Sponsor'];
        if (updatedFilters['product'].length === 0)
          updatedFilters['product'] = baseFilterList['product'];
        // if(updatedFilters['center'].length===0) updatedFilters['center'] = baseFilterList['center']
        let pmrData = [];

        for (let key in updatedFilters) {
          updatedFilters[key] = updatedFilters[key].filter(function (element) {
            return element !== undefined;
          });
        }

        let arr2 = [];
        for (let i = 0; i < dataRows.length; i++) {
          if (updatedFilters['appl_type'].includes(dataRows[i]['appl_type'])) {
            if (
              updatedFilters['subpart_approval_description'].includes(
                dataRows[i]['subpart_approval_description']
              )
            ) {
              if (updatedFilters['Sponsor'].includes(dataRows[i]['Sponsor'])) {
                if (
                  updatedFilters['product'].includes(dataRows[i]['product'])
                ) {
                  if (
                    updatedFilters['status_CMT'].includes(
                      dataRows[i]['status_CMT']
                    )
                  ) {
                    pmrData.push(dataRows[i]);

                    // }
                  }
                }
              }
            }
          }
        }
        props.handleFilterChange(filters);

        props.handleRowsChange(pmrData, filters.status_CMT);

        let tempFilterCounts = {
          appl_type: {},
          status_CMT: {},
          subpart_approval_description: {},
          Sponsor: {},
          product: {},
        };

        for (let i = 0; i < pmrData.length; i++) {
          if (pmrData[i]['appl_type'] in tempFilterCounts['appl_type']) {
            tempFilterCounts['appl_type'][pmrData[i]['appl_type']] =
              tempFilterCounts['appl_type'][pmrData[i]['appl_type']] + 1;
          } else {
            tempFilterCounts['appl_type'][pmrData[i]['appl_type']] = 1;
          }

          if (pmrData[i]['status_CMT'] in tempFilterCounts['status_CMT']) {
            tempFilterCounts['status_CMT'][pmrData[i]['status_CMT']] =
              tempFilterCounts['status_CMT'][pmrData[i]['status_CMT']] + 1;
          } else {
            tempFilterCounts['status_CMT'][pmrData[i]['status_CMT']] = 1;
          }

          if (
            pmrData[i]['subpart_approval_description'] in
            tempFilterCounts['subpart_approval_description']
          ) {
            tempFilterCounts['subpart_approval_description'][
              pmrData[i]['subpart_approval_description']
            ] =
              tempFilterCounts['subpart_approval_description'][
                pmrData[i]['subpart_approval_description']
              ] + 1;
          } else {
            tempFilterCounts['subpart_approval_description'][
              pmrData[i]['subpart_approval_description']
            ] = 1;
          }

          if (pmrData[i]['Sponsor'] in tempFilterCounts['Sponsor']) {
            tempFilterCounts['Sponsor'][pmrData[i]['Sponsor']] =
              tempFilterCounts['Sponsor'][pmrData[i]['Sponsor']] + 1;
          } else {
            tempFilterCounts['Sponsor'][pmrData[i]['Sponsor']] = 1;
          }

          if (pmrData[i]['PRODUCT'] in tempFilterCounts['product']) {
            tempFilterCounts['product'][pmrData[i]['product']] =
              tempFilterCounts['product'][pmrData[i]['product']] + 1;
          } else {
            tempFilterCounts['product'][pmrData[i]['product']] = 1;
          }
        }

        setFilterCounts(tempFilterCounts);
      } catch (error) {
        console.log(error);
      }
    }
    getData();
  }, [loading]);

  const handleFilterChange = async (filterType, value) => {
    console.log('selected value', filterType, value);
    if (filterType === 'category ') {
      setselectedCategory(value);
    }
    setResetFlag(false);
    let newFilters = filters;
    newFilters[filterType] = value;
    setFilters(newFilters);

    if (!loading) setLoading(true);
    else setLoading(false);
  };

  const handleResetFilters = async () => {
    let resetFilters = {
      appl_type: [],
      status_CMT: [],
      subpart_approval_description: [],
      Sponsor: [],
      product: [],
    };
    setResetFlag(true);
    setFilters(resetFilters);
    if (!loading) setLoading(true);
    else setLoading(false);
  };

  return (
    <div className={classes.root}>
      <Box display="flex" justifyContent="space-between">
        {/* <HeadingTypography text={"Filters"}/> */}
        <Typography className={classes.FilterText}>Filters</Typography>
        <Button onClick={handleResetFilters} className={classes.ResetButton}>
          Reset
        </Button>
      </Box>
      <AutoComplete
        title={
          String(filters['appl_type'].length) ===
            String(filterList['appl_type'].length) ||
          filters['appl_type'].length === 0
            ? 'Application Type' +
              ' (' +
              String(filterList['appl_type'].length) +
              ')'
            : 'Application Type' +
              ' (' +
              String(filters['appl_type'].length) +
              '/' +
              String(filterList['appl_type'].length) +
              ')'
        }
        selected={filters['appl_type']}
        filterType="appl_type"
        handleChange={handleFilterChange}
        options={filterList['appl_type']}
        placeholder="Typer here ..."
        filterCount={filterCounts['appl_type']}
      />
      <AutoComplete
        title={
          String(filters['status_CMT'].length) ===
            String(filterList['status_CMT'].length) ||
          filters['status_CMT'].length === 0
            ? 'PMR commitment Status' +
              ' (' +
              String(filterList['status_CMT'].length) +
              ')'
            : 'PMR commitment Status' +
              ' (' +
              String(filters['status_CMT'].length) +
              '/' +
              String(filterList['status_CMT'].length) +
              ')'
        }
        selected={filters['status_CMT']}
        filterType="status_CMT"
        handleChange={handleFilterChange}
        options={filterList['status_CMT']}
        placeholder="Typer here ..."
        filterCount={filterCounts['status_CMT']}
      />
      <AutoComplete
        title={
          String(filters['subpart_approval_description'].length) ===
            String(filterList['subpart_approval_description'].length) ||
          filters['subpart_approval_description'].length === 0
            ? 'Subpart Flag' +
              ' (' +
              String(filterList['subpart_approval_description'].length) +
              ')'
            : 'Subpart Flag' +
              ' (' +
              String(filters['subpart_approval_description'].length) +
              '/' +
              String(filterList['subpart_approval_description'].length) +
              ')'
        }
        selected={filters['subpart_approval_description']}
        filterType="subpart_approval_description"
        handleChange={handleFilterChange}
        options={filterList['subpart_approval_description']}
        placeholder="Typer here ..."
        filterCount={filterCounts['subpart_approval_description']}
      />
      <AutoComplete
        title={
          String(filters['Sponsor'].length) ===
            String(filterList['Sponsor'].length) ||
          filters['Sponsor'].length === 0
            ? 'Sponsor' + ' (' + String(filterList['Sponsor'].length) + ')'
            : 'Sponsor' +
              ' (' +
              String(filters['Sponsor'].length) +
              '/' +
              String(filterList['Sponsor'].length) +
              ')'
        }
        selected={filters['Sponsor']}
        filterType="Sponsor"
        handleChange={handleFilterChange}
        options={filterList['Sponsor']}
        placeholder="Typer here ..."
        filterCount={filterCounts['Sponsor']}
      />
      <AutoComplete
        title={
          String(filters['product'].length) ===
            String(filterList['product'].length) ||
          filters['product'].length === 0
            ? 'Product' + ' (' + String(filterList['product'].length) + ')'
            : 'Product' +
              ' (' +
              String(filters['product'].length) +
              '/' +
              String(filterList['product'].length) +
              ')'
        }
        selected={filters['product']}
        filterType="product"
        handleChange={handleFilterChange}
        options={filterList['product']}
        placeholder="Typer here ..."
        filterCount={filterCounts['product']}
      />
      {/* <AutoComplete
        title = { String(filters['center'].length) === String(filterList['center'].length) ||filters['center'].length === 0?
        "Center"+" ("+ String(filterList['center'].length) + ")"
        :"Center"+" ("+ String(filters['center'].length) + "/" + String(filterList['center'].length) + ")"} 
        selected = {filters['center']}
        filterType='center'
        handleChange={handleFilterChange}
        options={filterList['center']}
        placeholder = "Typer here ..."
      /> */}
    </div>
  );
}
