import React , { useEffect ,useState} from "react";
import PatentTableStyles from './PatentTable.styles'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography'
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';
import Zoom from '@material-ui/core/Zoom';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';


export default function PatentTable(props) {
  const classes = PatentTableStyles();

  const [productData , setProductData] = useState()
  const [patentData , setPatentData] = useState()

  useEffect(() => {
		async function getData() {
			try {
           setProductData(props.products) 
           setPatentData(props.patent)  
			} catch (e) {
				console.log(e)
			}
		}
		getData();
    }, [props])

  if(productData && patentData){
  return (
    <Paper className={classes.Paper}>
    <Table className={classes.Table} >
                    <TableHead className={classes.TableHead}>
                      <TableRow className={classes.StyledHeaderTableRow} >
                      <TableCell className={classes.StyledHeaderTableCell} >Patent</TableCell>
                        <TableCell className={classes.StyledHeaderTableCell} >Patent Expiration Date</TableCell>
                        {Object.keys(productData).map((key,index) => {
                                return(
                                        <TableCell className={classes.StyledHeaderTableCellMapped}>Product {key}</TableCell>
                                )
                              }
                        )}
                      </TableRow>
                    </TableHead>
                    <TableBody className={classes.TableBody}>
                        {patentData.map((item,index) => {	
                        return(
                          <TableRow className={classes.StyledTableRow} key="Marketing">
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                            {item["patent_no"]}
                              {item["patent_use_code"] && item["patent_definition"]?
                                <List component="nav" aria-labelledby="nested-list-subheader" className={classes.List}>
                                        {item["patent_use_code"].map((value,index) => {
                                          return(
                                              <ListItem button className={classes.ListItem}>
                                                <Tooltip TransitionComponent={Zoom} title={item["patent_definition"][index]}>
                                                  <ListItemText className={classes.ListItemText}>
                                                    <Typography className={classes.ListItemText}>{value}</Typography>
                                                  </ListItemText>
                                                </Tooltip>
                                              </ListItem>
                                          )
                                        })}
                                </List>
                                :
                                <div></div>
                              }
                            </TableCell>
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                            {item["patent_expire_date"]}
                            </TableCell>
                            {Object.keys(productData).map((key,index) => {
                              return(
                              <TableCell className={classes.StyledTableCellMapped}>
                                {item["product_no"].includes(key)?
                                          <CheckCircleIcon className={classes.StyledTableCellCheck}/>
                                          :
                                          <div></div>
                                }
                                </TableCell>
                              )
                              
                              })}
                          </TableRow>
                        )
                        })}
                    </TableBody>
      </Table>
      </Paper>
    )
  }
  else{
    return(
      <div></div>
    )
  }
}