import React, { useEffect, useState } from 'react';
import { Route, Switch, BrowserRouter as Router } from 'react-router-dom';
import ThemeProvider from '@material-ui/styles/ThemeProvider';
import Login from './Login';
import Landing from './Landing';
import theme from '../theme';
import '../App.css';
import { Auth } from 'aws-amplify';
import Help from './Help';
import { useHistory } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import ForgotPassword from './Login/ForgetPassword';
//context store
import { StateProvider } from '../store';
//error boundary
import ErrorBoundary from './Error/ErrorBoundary';
import { AmplifyAuthenticator, AmplifySignIn } from '@aws-amplify/ui-react';
import { AuthState, onAuthUIStateChange } from '@aws-amplify/ui-components';
// import VivproLogo from '../Images/svgs/logo';
import Viv321 from '../Images/Viv321.png';
//components
import Home from './Home';
import CardsPage from './CardsPage';
import Biologics from './Biologics';
import ClinicalTrials from './ClinicalTrials';
import Pmr from './PMR';
import GenericsTable from './GenericTable';
import Regulatory360Page from './Regu360Page';
import Status from './Status';
import ApprovalPathway from './ApprovalPathway';
// admin components
import AlertMessage from "./CustomComponents/Alerts";
import Navbar from "./AdminDashboard/components/Navbar";
import AdminHome from "./AdminDashboard/components/Home";
import DataEntry from "./AdminDashboard/components/DataEntry";
import ReviewPage from "./AdminDashboard/components/ReviewPage";
import Dashboard from "./AdminDashboard/components/Dashboard";
import PmrDataEntry from "./AdminDashboard/components/PmrDataEntry";
import PmrReviewPage from "./AdminDashboard/components/PmrReviewPage";


const App = () => {
  const history = useHistory();
  let [isAuthenticate, setAuthStatus1] = useState(false);
  let [isAuthenticating, setAuthenticatingStatus] = useState(false);
  const [authState, setAuthState] = React.useState();
  const [user, setUser] = React.useState();

  const setAuthStatus = (authenticated) => {
    setAuthStatus1(authenticated);
  };

  useEffect(() => {
    try {
      //This method refresh the token whenever necessary!
      // userDetails();
      onAuthUIStateChange((nextAuthState, authData) => {
        console.log(authData);
        setAuthState(nextAuthState);
        setUser(authData);
      });
    } catch (error) {
      console.log(error);
    }
  }, []);

  const logOutMethod = async () => {
    if (user) {
      await Auth.signOut();
      setAuthStatus(false);
      setAuthenticatingStatus(false);
      setUser(false);
      // history.push("/");
      window.location.href = "/";
    }
  };

  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <ErrorBoundary>
          <StateProvider>
            <AlertMessage />
            <ToastContainer
              position="top-right"
              autoClose={4000}
              hideProgressBar={false}
              newestOnTop={false}
              closeOnClick
              rtl={false}
              pauseOnFocusLoss
              draggable
              pauseOnHover
            />
            {authState === AuthState.SignedIn && user ? (
              <Router>
                {window.location.pathname.indexOf("admin") > -1 ? (
                  <Navbar />
                ) : (
                  <>
                    {window.location.pathname === "/help" ||
                    window.location.pathname === "/status" ? null : (
                      // <Navbar></Navbar>
                      <Landing logOutMethod={logOutMethod} />
                    )}
                  </>
                )}

                <Switch>
                  <Route key={1} exact path="/" component={Home} />
                  <Route
                    key={2}
                    path="/search/:searchType/:module/:category/:search/:source"
                    component={CardsPage}
                  />
                  <Route
                    key={3}
                    path="/search/:searchType/:module"
                    component={CardsPage}
                  />
                  <Route
                    key={4}
                    exact
                    path="/biologics"
                    component={Biologics}
                  />
                  <Route
                    key={5}
                    path="/clinicalTrials/:module/:category/:search"
                    component={ClinicalTrials}
                  />
                  <Route key={6} path="/pmr/impact" exact component={Pmr} />
                  <Route
                    key={6}
                    path="/pmr/surveillance"
                    exact
                    component={Pmr}
                  />
                  <Route
                    key={7}
                    path="/table/generics/:type/:number"
                    component={GenericsTable}
                  />
                  <Route
                    key={8}
                    path="/regulatory360/:module/:type/:number"
                    component={Regulatory360Page}
                  />
                  <Route key={9} path="/admin/status" component={Status} />
                  <Route key={10} path="/505b2" component={ApprovalPathway} />
                  <Route key="help" path="/help" exact component={Help}>
                    <Help logOutMethod={logOutMethod} />
                  </Route>
                  <Route
                    key="login"
                    path="/registration"
                    exact
                    component={Login}
                  >
                    <Login logOutMethod={logOutMethod} />
                  </Route>
                  <Route
                    exact
                    path="/forgotpassword"
                    render={(props) => <ForgotPassword {...props} />}
                  />
                  <Route
                    key={11}
                    path="/admin"
                    exact={true}
                    component={AdminHome}
                  />
                  <Route
                    key={12}
                    path="/admin/reviews"
                    exact={true}
                    component={DataEntry}
                  />
                  <Route
                    key={13}
                    path="/admin/:app/:subtype/:subno/:doctype"
                    exact={true}
                    component={ReviewPage}
                  />
                  <Route
                    key={14}
                    path="/admin/dashboard"
                    exact={true}
                    component={Dashboard}
                  />
                  <Route
                    key={15}
                    path="/admin/pmr/review"
                    exact={true}
                    component={PmrDataEntry}
                  />
                  <Route
                    key={15}
                    path="/admin/pmr/review/:key"
                    exact={true}
                    component={PmrReviewPage}
                  />
                </Switch>
              </Router>
            ) : (
              <div className="Login">
                <div
                  style={{
                    position: 'fixed',
                    left: '35%',
                    top: '10%',
                    bottom: '15%',
                    right: '35%',

                    height: '100%',
                  }}
                >
                  <div>
                    <div className="LoginHeader">
                      {/* <VivproLogo /> */}
                      <img src={Viv321} alt="logo" width={250} height={125} />
                    </div>
                    <AmplifyAuthenticator>
                      <AmplifySignIn
                        slot="sign-in"
                        hideSignUp="true"
                      ></AmplifySignIn>
                    </AmplifyAuthenticator>
                  </div>
                </div>
              </div>
            )}
          </StateProvider>
        </ErrorBoundary>
      </ThemeProvider>
    </div>
  );
};

export default App;
