import React, { Fragment } from 'react';
//external libs
import { ResponsiveBar } from '@nivo/bar';

const CustomBarGraph = ({ keys, index, data, margin, colors }) => {
  return (
    <Fragment>
      <ResponsiveBar
        data={data && data}
        enableGridY={false}
        keys={keys}
        indexBy={index}
        margin={margin || { top: 30, right: 50, bottom: 50, left: 30 }}
        padding={0.1}
        valueScale={{ type: 'linear' }}
        indexScale={{ type: 'band', round: true }}
        colors={colors || ['#0E5A94', '#318AC8', '#008B8B', '#3AB09E', '#48E0A6', '#1C2D59', '#20A1C7']}
        colorBy='index'
        borderRadius={2}
        theme={{
          axis: {
            fontSize: "14px",
            tickColor: "#000",
            ticks: {
              line: {
                stroke: "#555555"
              },
              text: {
                fill: "#1FBDCA",
                fontWeight: 'bold'
              }
            }
          },
        }}
        axisTop={null}
        axisRight={null}
        axisBottom={{
          tickSize: 0,
          tickPadding: 5,
          tickRotation: 90,
          legend: '',
          legendPosition: 'middle',
          legendOffset: 0
        }}
        axisLeft={{
          tickSize: 0,
          tickPadding: 5,
          tickRotation: 0,
          legend: '',
          legendPosition: 'middle',
          legendOffset: 0
        }}
        enableLabel={false}
        labelSkipWidth={12}
        labelSkipHeight={12}
        labelTextColor={['#fff']}
        animate={true}
        motionStiffness={90}
        motionDamping={15}
      />
    </Fragment>
  );
}

export default CustomBarGraph;