import React , {useEffect,useState} from 'react';
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import AutoCompleteStyles from './AutoComplete.styles'
import Typography from '@material-ui/core/Typography';
import Collapse from "@material-ui/core/Collapse";
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Checkbox from '@material-ui/core/Checkbox';
import Box from '@material-ui/core/Box';
import AddIcon from '@material-ui/icons/Add';

const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;


export default function AutoComplete(props) {
  

  useEffect(() => {
    async function getData() {}
    getData();
  }, [props]);

  const classes = AutoCompleteStyles();
  const [filterExpanded, setFilterExpanded] = useState(false);

  const handleFilterChange = (value) => {
    props.handleChange(value)
  }

  return (
    <div className={classes.root}>
      <Box  display="flex" justifyContent="space-between"
      onClick={() => {
        if (filterExpanded) setFilterExpanded(false);
        else setFilterExpanded(true);
      }}
      style={{cursor:'pointer'}}>
      <Typography className = {classes.FilterTitle}
      aria-expanded={filterExpanded}
      >
          {props.title}
      </Typography>
      <AddIcon className = {classes.FilterTitleButton}/>
      </Box>
      <Collapse in={filterExpanded} timeout="auto" unmountOnExit>
      <Autocomplete
        multiple
        defaultValue ={props.selected ? props.selected : null}
        // name = {props.filterType}
        onChange = {(event,value)=>handleFilterChange(value)}
        id="tags-outlined"
        options={props.options}
        // options={props.options.sort((a, b) => -b.firstLetter.localeCompare(a.firstLetter))}
        // groupBy={(option) => option.firstLetter}
        // getOptionLabel={(option) => option}
        disableCloseOnSelect
        renderOption={(option, { selected }) => (
          <React.Fragment>
            {parseInt(props.filterCount[option])>0?
             <div>
              <Box  display="flex">
            <Checkbox
              icon={icon}
              checkedIcon={checkedIcon}
              style={{ marginRight: 8 }}
              checked={selected}
              className={classes.FilterChecked}
            />
            <Typography className={classes.FilterOption}>
              {option + "  ("+props.filterCount[option]+ ")"}
            </Typography>
            </Box>
            </div>
            :
            <div>
            <Box  display="flex">
            <Checkbox
              icon={icon}
              checkedIcon={checkedIcon}
              style={{ marginRight: 8 }}
              checked={selected}
              className={classes.FilterChecked}
            />
            <Typography className={classes.FilterOption}>
            {option}
            </Typography>
            </Box>
            </div>}
          </React.Fragment>
        )}
        className = {classes.AutoCompleteSearch}
        // filterSelectedOptions
        renderInput={(params) => (
          <TextField
            {...params}
            className = {classes.AutoCompleteSearchTextField}
            variant="outlined"
            placeholder={props.placeholder}
          />
        )}
      />
      </Collapse>
    </div>
  );
}


const ais = [ "Everolimus", "Lasmiditan Succinate", "Rofecoxib", "Sumatriptan Succinate", "Vigabatrin"]
