import React, { useState, useEffect } from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import { render } from 'react-dom';
import { getTimeLinePage360 } from '../ServiceApi/services';
import styled from '@emotion/styled';
import { Tree, TreeNode } from 'react-organizational-chart';
import NetworkDaigramStyles from './NetworkDiagram.styles';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import CancelIcon from '@material-ui/icons/Cancel';
import IconButton from '@material-ui/core/IconButton';
import LaunchOutlinedIcon from '@material-ui/icons/LaunchOutlined';
import '../../Images/021505_s000_Keppra_Prntlbl.pdf';
import { getLabelComparision } from '../ServiceApi/services';
import axios from 'axios';
import Typography from '@material-ui/core/Typography';
import { SemipolarLoading } from 'react-loadingg';
import { ToastContainer, toast } from 'react-toastify';
import LabelComapreIcon from '../../Images/brightness-and-contrast.svg';
import CenterBallN from '../../Images/centerBallNext.svg';

function NetworkDaigram(props) {
  const classes = NetworkDaigramStyles();
  const [netDiagramRes, setnetDiagramRes] = useState('');
  const [NetFlag, setNetFlag] = useState(false);
  const [PDFData, setPDFData] = useState(false, {});
  const [viewerURL, setviewerURL] = useState();
  const [parentNode, setparentNode] = useState();
  const [childNode, setchildNode] = useState();
  const [ChildNda, setChildNda] = useState();
  const [loading, setLoading] = useState(false);
  const [Errorstate, setError] = useState(false);
  const [Comparision, setComparision] = useState();
  const [next, setnext] = useState(false, {});

  useEffect(() => {
    const netwDiagram = props.timeLinePageData.data.success.network_diagram;
    setparentNode('');
    setchildNode('');
    for (let i = 0; i < netwDiagram.length; i++) {
      if (netwDiagram[i].flag === 'p') {
        setparentNode(netwDiagram[i]);
      }
      if (netwDiagram[i].flag === 'c') {
        setchildNode(netwDiagram[i]);
      }
    }
    setnetDiagramRes(netwDiagram);

    handleNetflag();
  }, [props]);

  const StyledNode = styled.div`
    padding: 3vh;
    border-radius: 8px;
    display: inline-block;
    cursor: pointer;
    cursor: pointer;
    color: #0352cc;
    margin: 10px;
    background-color: #f7fbff;
    border-radius: 20px;
    box-shadow: 0 0 4px rgb(220, 220, 220, 0.5);
  `;

  const handleNetflag = () => {
    for (let i = 0; i < netDiagramRes.length; i++) {
      if (i.flag === 'p') {
        setNetFlag(true);
      }
    }
  };

  const handleComparision = async (childNda) => {
    setLoading(true);
    setChildNda(childNda);
    const pdfData = {
      parent_object: parentNode.nda,
      child_object: childNda,
    };
    var Base64 = require('js-base64').Base64;
    const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
    const hash = Base64.encode(tok);
    const Basic = 'Basic ' + hash;

    // const res = await axios.post(
    //   // `https://ria-api.vivpro.ai:8443/v2/comparison/viewer/pdf`,
    //   `https://ria-api-staging.vivpro.ai:8443/v2/comparison/viewer/pdf`,
    //   pdfData,
    //   { headers: { Authorization: Basic } }
    // );

    let res;
    await getLabelComparision(pdfData)
      .then((response) => {
        res = response;
      })
      .catch((error) => {
        console.log(error);
      });
    if (res.data.success !== null) {
      setPDFData([true, {}]);
      setviewerURL(res.data.success);

      setError(false);
    } else {
      setPDFData([false, {}]);
      toast.error(res.data.error);
      setError(true);
    }

    setLoading(false);
  };

  return (
    <div>
      {childNode === '' || parentNode === '' ? (
        <Typography className={classes.StyledErrorTypography}>
          No RLD Information data available
        </Typography>
      ) : (
        <div>
          {netDiagramRes ? (
            <div className={classes.networkDiagram}>
              {netDiagramRes.map((item) => {
                return item.flag === 'p' ? (
                  <Tree
                    className={classes.ParentNode}
                    lineWidth={'2px'}
                    lineColor={'grey'}
                    lineBorderRadius={'10px'}
                    label={
                      <StyledNode>
                        <div className={classes.ndaNode}>{item.nda}</div>
                        <div className={classes.Node}>
                          {item.manufacturer}|{item.approval_date}
                        </div>
                        <div className={classes.Node}>
                          {item.formulation_route}
                        </div>
                        <div className={classes.Node}>
                          Marketing Status: {item.marketingstatus}
                        </div>
                      </StyledNode>
                    }
                  >
                    {netDiagramRes.map((item2) => {
                      return item2.flag === 'c' ? (
                        <TreeNode
                          label={
                            <StyledNode value={item2.nda}>
                              <div
                                className={classes.ndaNode}
                                onClick={() =>
                                  props.handleNetworkChild(item2.nda)
                                }
                              >
                                {' '}
                                {item2.nda}
                              </div>
                              <div className={classes.Node}>
                                {item2.manufacturer}|{item2.approval_date}
                              </div>
                              <div className={classes.Node}>
                                {item2.formulation_route}
                              </div>
                              <div className={classes.Node}>
                                Marketing Status: {item2.marketingstatus}
                              </div>
                              <div
                                onClick={() => handleComparision(item2.nda)}
                                className={classes.compareL}
                              >
                                <img
                                  style={{ width: '15px' }}
                                  src={LabelComapreIcon}
                                ></img>
                                <Typography className={classes.labelTitle}>
                                  Compare Labels
                                </Typography>
                              </div>
                            </StyledNode>
                          }
                        ></TreeNode>
                      ) : (
                        <div></div>
                      );
                    })}
                  </Tree>
                ) : (
                  <div></div>
                );
              })}
            </div>
          ) : (
            <div className={classes.networkDiagram}>No data to display</div>
          )}
        </div>
      )}

      <Dialog
        fullWidth={true}
        maxWidth={'lg'}
        onClose={() => {
          setPDFData([false, {}]);
        }}
        aria-labelledby="customized-dialog-title"
        open={PDFData[0] !== undefined ? PDFData[0] : false}
        classes={{ paper: classes.dialog }}
      >
        <DialogTitle style={{ padding: '0 24px' }}>
          <Grid container>
            <Grid
              container
              item
              xs={11}
              justify="center"
              alignItems="center"
              className={classes.LabelTitle}
            >
              <img
                style={{ width: '33px', paddingRight: '10px' }}
                src={LabelComapreIcon}
              ></img>
              LABEL COMPARISON
            </Grid>
            <Grid container item xs={1} justify="flex-end" alignItems="center">
              <IconButton
                aria-label="delete"
                className={classes.ModalCloseIconButton}
                onClick={() => {
                  setPDFData([false, {}]);
                }}
              >
                <CancelIcon className={classes.ModalCloseIcon} />
              </IconButton>
            </Grid>
          </Grid>
          <Grid container item xs={12} justify="center" alignItems="center">
            {/*  <Grid
              container
              item
              xs={6}
              justify="center"
              alignItems="center"
              className={classes.Label}
            >
              {parentNode && parentNode.nda}
              </Grid> */}
            {/* <Grid
              container
              item
              xs={6}
              justify="center"
              alignItems="center"
              className={classes.Label}
            >
              {ChildNda && ChildNda}
            </Grid> */}
          </Grid>
        </DialogTitle>
        <DialogContent className={classes.Dialog}>
          <Grid xs={12} container justify="center" alignItems="center" item>
            <Grid container item xs={12} justify="center" alignItems="center">
              <Grid
                container
                item
                xs={5}
                justify="center"
                alignItems="center"
                className={classes.Label}
              >
                <Grid
                  xs={12}
                  container
                  justify="center"
                  alignItems="center"
                  item
                  className={classes.selectLabelC}
                >
                  {parentNode && parentNode.nda}
                </Grid>
              </Grid>
              <Grid container item xs={1} justify="center" alignItems="center">
                <span
                  style={{
                    borderTop: '1px solid #a2a2a2',
                    width: '100%',
                    marginBottom: '-30%',
                  }}
                ></span>
                <img style={{ width: '28px' }} src={CenterBallN}></img>
              </Grid>
              <Grid
                container
                item
                xs={5}
                justify="center"
                alignItems="center"
                className={classes.Label}
              >
                <Grid
                  xs={12}
                  container
                  justify="center"
                  alignItems="center"
                  item
                  className={classes.selectLabel}
                >
                  {' '}
                  {ChildNda && ChildNda}
                </Grid>
              </Grid>
            </Grid>
            <Grid
              xs={12}
              container
              justify="center"
              alignItems="flex-start"
              item
            >
              <div style={{ height: '70vh', width: '100vw' }}>
                {loading ? (
                  <SemipolarLoading color="#0352CC" />
                ) : !Errorstate && viewerURL ? (
                  <iframe
                    style={{ height: '100%', width: '100%' }}
                    src={viewerURL}
                    allowFullScreen
                  ></iframe>
                ) : (
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'center',
                      alignItems: 'center',
                      height: '100%',
                    }}
                  >
                    <Typography className={classes.StyledErrorTypography}>
                      Sorry , we couldn't find any comparision to{' '}
                      {parentNode && parentNode.nda} and {ChildNda} label.
                      Please try other document.
                    </Typography>
                  </div>
                )}
              </div>
            </Grid>
          </Grid>
        </DialogContent>
      </Dialog>
    </div>
  );
}
export default NetworkDaigram;
