import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import DescriptionIcon from '@material-ui/icons/Description';
import DownloadLogo from '../../../../Images/downloadDark.svg';
import NotLike from '../../../../Images/NotLike.svg';
import NotDislike from '../../../../Images/NotDislike.svg';
import Like from '../../../../Images/Like.svg';
import Dislike from '../../../../Images/Dislike.svg';
import BotCardStyles from './BotCard.styles';
import Tooltip from '@material-ui/core/Tooltip';
import Box from '@material-ui/core/Box';
import Zoom from '@material-ui/core/Zoom';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import axios from 'axios';
import { getFeedbackOnSuggestedAns } from '../../../ServiceApi/services';

export default function BotCard(props) {
  const classes = BotCardStyles();

  useEffect(() => {
    try {
      window.scrollTo(0, 0);
      // document.getElementById('HEADER').scrollIntoView();
      // ReactDOM.findDOMNode(this).scrollIntoView();
    } catch (e) {}
  }, [props]);

  const [like, setLike] = useState([false, false]);

  const handleSetLikeButton = async (queryId, resultId, value) => {
    if (value === 'RELEVANT') {
      setLike([true, false]);
      var Base64 = require('js-base64').Base64;
      const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
      const hash = Base64.encode(tok);
      const Basic = 'Basic ' + hash;
      let requestOptions = {
        'query-id': queryId,
        'result-id': resultId,
        'user-reaction': 'RELEVANT',
      };
      console.log(requestOptions);
      // let response = await axios.post(
      //   'https://ria-api.vivpro.ai:8443/v2/clignosis/bot/submit_feedback',
      //   // 'http://localhost:5000/ria/bot/submit_feedback',
      //   requestOptions,
      //   { headers: { Authorization: Basic } }
      // );
      // if(like[0]===false) setLike([true,false])
      // else setLike([false,false])
      let response;
      await getFeedbackOnSuggestedAns(requestOptions)
        .then((res) => {
          response = res;
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      setLike([false, true]);
      var Base64 = require('js-base64').Base64;
      const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
      const hash = Base64.encode(tok);
      const Basic = 'Basic ' + hash;
      let requestOptions = {
        'query-id': queryId,
        'result-id': resultId,
        'user-reaction': 'NOT_RELEVANT',
      };
      // let response = await axios.post(
      //   'https://ria-api.vivpro.ai:8443/v2/clignosis/bot/submit_feedback',
      //   // 'http://localhost:5000/ria/bot/submit_feedback',
      //   requestOptions,
      //   { headers: { Authorization: Basic } }
      // );
      // if(like[1]===false) setLike([false,true])
      // else setLike([false,false])
      let response;
      await getFeedbackOnSuggestedAns(requestOptions)
        .then((res) => {
          response = res;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  return (
    <Card className={classes.Card} id="botCards">
      <CardContent className={classes.CardContent}>
        <Typography className={classes.ResponseText}>
          {props.data.text}
        </Typography>

        {/* <Divider className={classes.Divider} variant="middle" /> */}

        <Grid
          container
          justify="space-between"
          alignItems="center"
          className={classes.BottomGrid}
        >
          <Grid item={9} justify="flex-start" alignItems="center">
            {props.data.activeIngredient.length > 20 ? (
              <Tooltip
                TransitionComponent={Zoom}
                title={props.data.activeIngredient}
              >
                <Typography className={classes.StyledTypography}>
                  {props.data.activeIngredient.substring(0, 20)}...
                </Typography>
              </Tooltip>
            ) : (
              <Typography className={classes.StyledTypography}>
                {props.data.activeIngredient}
              </Typography>
            )}
          </Grid>
          <Grid item={3} justify="flex-end" alignItems="center">
            <IconButton
              aria-label="show 0"
              color="primary"
              className={classes.BotDocumentIconButton}
              onClick={() => window.open(props.data.documentURL, '_blank')}
            >
              <img src={DownloadLogo}></img>
            </IconButton>
          </Grid>
        </Grid>

        <Grid
          container
          justify="space-evenly"
          alignItems="center"
          className={classes.BottomGrid}
        >
          <Grid item>
            {/* <Box display="flex" flexDirection="row">
            <Typography className={classes.StyledTypographyCapsuleCircle}>
                <FiberManualRecordIcon  />

            </Typography>
            <Typography className={classes.StyledTypographyCapsule}>
                {props.data.application}
            </Typography>
            
            </Box> */}
            <Typography className={classes.StyledTypographyCapsule}>
              <span className={classes.StyledTypographyCapsuleCircles}>
                {/* <i class="fas fa-circle"></i> */}
                {/* <IconButton size="small" className={classes.StyledTypographyCapsule}> */}
                <FiberManualRecordIcon
                  className={classes.StyledTypographyCapsuleCircle}
                />
                {/* </IconButton> */}
              </span>
              {props.data.application}
            </Typography>
          </Grid>
          <Grid item justify="center" alignItems="center">
            <Typography className={classes.StyledTypographyCapsule}>
              <span className={classes.StyledTypographyCapsuleCircles}>
                <FiberManualRecordIcon
                  className={classes.StyledTypographyCapsuleCircle}
                />
              </span>
              {props.data.submissionType}
            </Typography>
          </Grid>
          <Grid item justify="center" alignItems="center">
            <Typography className={classes.StyledTypographyCapsule}>
              <span className={classes.StyledTypographyCapsuleCircles}>
                <FiberManualRecordIcon
                  className={classes.StyledTypographyCapsuleCircle}
                />
              </span>
              {props.data.submissionDate}
            </Typography>
          </Grid>
          <Grid item justify="center" alignItems="center">
            <Box
              display="flex"
              justifyContent="center"
              className={classes.BotLikeButtonBox}
              id="botLikeButtons"
            >
              <IconButton
                className={classes.BotLikeButton}
                onClick={() =>
                  handleSetLikeButton(
                    props.data.queryId,
                    props.data.resultId,
                    'RELEVANT'
                  )
                }
              >
                <img src={like[0] ? Like : NotLike}></img>
              </IconButton>
              <IconButton
                className={classes.BotLikeButton}
                onClick={() =>
                  handleSetLikeButton(
                    props.data.queryId,
                    props.data.resultId,
                    'NOT_RELEVANT'
                  )
                }
              >
                <img src={like[1] ? Dislike : NotDislike}></img>
              </IconButton>
            </Box>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}
