import React, { useContext, useState, useEffect } from "react";
import { Auth } from "aws-amplify";
//material ui imports
import Box from "@material-ui/core/Box";
import Link from "@material-ui/core/Link";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
// context
import Store from "../../../store";
import Actions from "../../../store/actions";
// other components
import Styles from "../styles/Home.styles";

const Home = () => {
  const classes = Styles();
  const { state, dispatch } = useContext(Store);
  const [user, setUser] = useState();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const user = await Auth.currentUserInfo();
        setUser(user.attributes);
        await dispatch({ type: Actions.SET_MODULE, value: "admin" });
      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  return (
    <Box className={classes.root}>
      <Container maxWidth="lg" className={classes.container}>
        <Box
          display="flex"
          flexDirection="row"
          justifyContent="center"
          alignItems="center"
        >
          <Typography className={classes.heading}>Welcome back </Typography>
          <Typography className={classes.name}>
            {state.userStub && state.userStub["custom:user"]},
          </Typography>
        </Box>
        <Typography align="center" className={classes.subHeading}>
          These are the things you can do...
        </Typography>
        <Divider className={classes.divider} />
        <Grid container className={classes.grids}>
          <Grid item xs={12} lg={4}>
            <Box padding={1}>
              <Link href="/admin/reviews" underline="none">
                <Paper elevation={2} className={classes.paper}>
                  <Box
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    className={classes.paperBox}
                  >
                    <Typography className={classes.cardText}>
                      Data Curation
                    </Typography>
                  </Box>
                </Paper>
              </Link>
            </Box>
          </Grid>
          {user && user["custom:role"] === "Admin" ? (
            <Grid item xs={12} lg={4}>
              <Box padding={1}>
                <Link href="/admin/dashboard" underline="none">
                  <Paper elevation={2} className={classes.paper}>
                    <Box
                      display="flex"
                      justifyContent="center"
                      alignItems="center"
                      className={classes.paperBox}
                    >
                      <Typography className={classes.cardText}>
                        Dashboard
                      </Typography>
                    </Box>
                  </Paper>
                </Link>
              </Box>
            </Grid>
          ) : null}
          <Grid item xs={12} lg={4}>
            <Box padding={1}>
              <Link href="/admin/pmr/review" underline="none">
                <Paper elevation={2} className={classes.paper}>
                  <Box
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    className={classes.paperBox}
                  >
                    <Typography className={classes.cardText}>
                      PMR Data Curation
                    </Typography>
                  </Box>
                </Paper>
              </Link>
            </Box>
          </Grid>
          <Grid item xs={12} lg={4}>
            <Box padding={1}>
              <Link href="/admin/status" underline="none">
                <Paper elevation={2} className={classes.paper}>
                  <Box
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    className={classes.paperBox}
                  >
                    <Typography className={classes.cardText}>Status</Typography>
                  </Box>
                </Paper>
              </Link>
            </Box>
          </Grid>
        </Grid>
      </Container>
    </Box>
  );
};

export default Home;
