import React, { Component, useEffect, useState } from 'react';

import ApplicationContentStyles from './ApplicationContent.styles';
import data from './data.json';
import Grid from '@material-ui/core/Grid';
import { ResponsiveLine } from '@nivo/line';
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  LabelList,
  ResponsiveContainer,
} from 'recharts';

// import BarChart from 'react-bar-chart';

function StudyDuration(props) {
  const classes = ApplicationContentStyles();

  const [dataList, setDataList] = useState();
  const [datares, setDatares] = useState();
  //   const [graphType, setGraphType] = useState();
  //   const [keyList, setKeyList] = useState();
  //   const [key, setKey] = useState('Title');
  //   const [title, setTitle] = useState();
  //   const [message, setMessage] = useState();
  const [loading, setLoading] = useState(true);
  const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];
  useEffect(() => {
    async function getData() {
      try {
        // const query = {
        //   chartId: String(props.data),
        // };
        // const response = await axios.post(
        //   'https://ria-staging.clignosis.ai:8443/clignosis/fetch-stats',
        //   query
        // );

        // console.log(response.data.success);
        let dataObj = props.data;
        //let dataObj = data[props.data]
        let unique = [...new Set(props.studyDurationRes)];
        console.log(dataObj);

        const result = [];
        const map = new Map();
        for (const item of unique) {
          if (!map.has(item.title)) {
            map.set(item.title, true); // set any value to Map
            result.push({
              title: item.title,
              value: item.value,
            });
          }
        }
        // console.log('unique', result);

        setDatares(result);
        // setGraphType(dataObj['graphType']);
        setDataList(dataObj);
        // setTitle(dataObj['key']);
        // setMessage(dataObj['message']);
        // setKeyList(dataObj['keyList']);
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, [props.data]);

  if (loading === true) {
    return (
      <div>
        <h4 style={{ margin: 'unset' }}>Study Summary</h4>
      </div>
    );
  } else {
    return (
      <Grid>
        <h5 style={{ margin: 'unset' }}>Study Duration</h5>
        {props.studyDurationRes && props.studyDurationRes.length > 0 ? (
          <div
            style={{
              paddingBottom: '75.25%' /* 16:9 */,
              position: 'relative',
              height: 0,
            }}
          >
            <div
              style={{
                position: 'absolute',
                top: '0',
                left: '0',
                width: '100%',
                height: '100%',
              }}
            >
              <ResponsiveContainer width="99%">
                <BarChart
                  data={datares && datares}
                  margin={{
                    top: 15,
                    right: 25,
                    left: 5,
                    bottom: 5,
                  }}
                  interval={{ XAxis: 0 }}
                >
                  <Tooltip />

                  <XAxis dataKey="title" tick={{ fontSize: 10 }} />
                  <YAxis
                    tick={{ fontSize: 10 }}
                    label={{
                      value: 'Study Duration in weeks',
                      angle: -90,
                      fontSize: 10,
                    }}
                    position="insideLeft"
                    style={{ textAnchor: 'end', margin: '10' }}
                    angle={-45}
                    textAnchor="middle"
                  />

                  <Bar dataKey="value" fill="#8884d8">
                    {props.studyDurationRes &&
                      props.studyDurationRes.map((entry, index) => (
                        <Cell
                          key={`cell-${index}`}
                          fill={COLORS[index % COLORS.length]}
                        />
                      ))}
                  </Bar>
                </BarChart>
              </ResponsiveContainer>
            </div>
          </div>
        ) : (
          <div></div>
        )}
      </Grid>
    );
  }
}

export default StudyDuration;
