import React, { Fragment, useContext } from 'react';
//material ui imports
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
//other components
import PieChartStyles from '../styles/PieCharts.styles';
import PlusIcon from '../../../Images/svgs/plus';
import CustomPieChart from '../../CustomComponents/Graphs/CustomPieChart';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';

const PieCharts = ({ data }) => {
  const classes = PieChartStyles();
  const { state, dispatch } = useContext(Store);

  const addPieCharts = async () => {
    await dispatch({ type: Actions.SET_CART_ITEMS, value: state.cartItems + 1 });
    await dispatch({ type: Actions.SET_REPORT, value: { id: 'Immunogenicity Pie Charts', data: data && data } })
  }

  return (
    <Fragment>
      <Box mr={0} display="flex" alignItems="center" justifyContent="space-between" className={classes.root}>
        <Typography className={classes.headings}>Reporting status of immunogenicity data components - Reported vs No Report (NR) vs No Label (NL) vs No Assessment (NA)</Typography>
        <Typography onClick={addPieCharts} className={classes.add}>
          <PlusIcon /> Add to report
        </Typography>
      </Box>
      <Box mt={2} display="flex" flexDirection="row" justifyContent="" alignItems="flex-start" className={classes.constainer}>
        {data ? (
          data && data.map((item) => {
            return (
              <Box display="flex" flexDirection="column" justifyContent="" className={classes.container}>
                <Typography className={classes.text}>{item[0].label}</Typography>
                <CustomPieChart data={item} />
              </Box>
            )
          })
        ) : (
            <Box display="flex" justifyContent="space-evenly" flexDirection="row" className={classes.skeleton}>
              <Skeleton variant="circle" width={200} height={200} />
              <Skeleton variant="circle" width={200} height={200} />
              <Skeleton variant="circle" width={200} height={200} />
              <Skeleton variant="circle" width={200} height={200} />
              <Skeleton variant="circle" width={200} height={200} />
            </Box>

          )}


      </Box>
    </Fragment>
  );
}

export default PieCharts;