import React, { useState, useEffect } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import CustomBreadcrumbsStyles from './CustomBreadcrumbs.styles';
import Typography from '@material-ui/core/Typography';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';
import { CapsuleTypography, CapsuleLightTypography } from '../Typography';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

const CustomSearchBreadcrumbs = (props) => {
  const classes = CustomBreadcrumbsStyles();
  const history = useHistory();
  const location = useLocation();
  const [search, setSearch] = useState('');
  const [source, setSource] = useState('');
  const [category, setCategory] = useState('');
  const [lastItem, setLastItem] = useState();
  const [navigation, setNavigation] = useState();

  useEffect(() => {
    async function getData() {
      try {
        setNavigation(JSON.parse(localStorage.getItem('navigation')));
        let quickSearch = props.search.split('/');
        if (quickSearch[0].charAt(0) === '?') {
          quickSearch[0] = quickSearch[0].substring(1);
        }
        if (quickSearch[1] == 'rangeSearch') {
          setSearch('results');
        } else {
          setSearch(quickSearch[1]);
        }

        const categoryVals = {
          indications_and_usage: 'TA/Indication',
          substance_name: 'Active Ingredient',
          brand_name: 'Trade Name',
          formulation: 'Formulation',
          route: 'Route',
          approval_date: 'Approval Date',
        };

        setCategory(categoryVals[quickSearch[0]]);
        const sourceVals = {
          US: 'US FDA - Drug & Biologics',
          EU: 'EU EMA - Drug & Biologics',
          '': 'US FDA - Devices',
        };
        if (quickSearch[2].includes('?')) {
          const sourceSplit = quickSearch[2].split('?');
          setSource(sourceVals[sourceSplit[0]]);
        } else {
          setSource(sourceVals[quickSearch[2]]);
        }
        if (props.lastItem) {
          setLastItem(props.lastItem);
        }
      } catch (e) {}
    }
    getData();
  }, [props]);

  function handleHomeClick(event) {
    event.preventDefault();
    history.push({
      pathname: '/',
    });
  }
  function handleAppsClick(url) {
    // event.preventDefault();
    // console.log(url)
    localStorage.setItem(
      'navigation',
      JSON.stringify({ cards: navigation.cards })
    );
    // history.goBack()
    history.push(url);
    // history.push({
    //   pathname: '/apps',})
  }

  const handleRegulatoryClick = (url) => {
    history.push(url);
  };
  const handleClinicalClick = (url) => {
    history.push(url);
  };

  return (
    <div>
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
        className={classes.Breadcrumbs}
      >
        <Link
          color="inherit"
          onClick={handleHomeClick}
          className={classes.link}
        >
          <Typography>Home</Typography>
        </Link>

        <Link
          color="inherit"
          onClick={() => handleAppsClick(navigation && navigation.cards.url)}
          className={classes.link}
        >
          {/*    {console.log(
            'cards value',
            navigation && navigation.cards.value,
            navigation && navigation.cards,
            navigation && navigation.cards.advValue
       )} */}
          <Typography>
            {(navigation && navigation.cards.value) || null}
          </Typography>
        </Link>
        {navigation && navigation.regulatory ? (
          <Link
            color="inherit"
            onClick={() =>
              handleRegulatoryClick(navigation && navigation.regulatory.url)
            }
            className={classes.link}
          >
            <Typography>
              {(navigation && navigation.regulatory.value) || null}
            </Typography>
          </Link>
        ) : null}
        {navigation && navigation.clinicalTrial ? (
          <Link
            color="inherit"
            onClick={() =>
              handleClinicalClick(navigation && navigation.clinicalTrial.url)
            }
            className={classes.link}
          >
            <Typography>
              {(navigation && navigation.clinicalTrial.value) || null}
            </Typography>
          </Link>
        ) : null}

        {/* <Link className={classes.link} onClick={handleAppsClick}>
        <CapsuleTypography text={source} />
      </Link>
      <Link className={classes.link} onClick={handleAppsClick}>
        <CapsuleTypography text={category} />
      </Link> */}
        {/* {lastItem ?
        (
          // <Link className={classes.link} onClick={handleAppsClick}>
          //   <CapsuleTypography text={search} />
          // </Link>
          <Link className={classes.link} onClick={handleAppsClick}>
            kmfe
            <CapsuleTypography text={source+" - "+category+" - "+search} />
          </Link>
        )
        :
        <CapsuleLightTypography text={source+" - "+category+" - "+search} />}
      {lastItem ?
        (
          <CapsuleLightTypography text={lastItem} />
        )
        :
        null} */}
      </Breadcrumbs>
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
        className={classes.Breadcrumbs}
      >
        {navigation && navigation.cards.advValue ? (
          <Typography
            style={{
              fontStyle: 'italic',
              fontWeight: '600',
              fontSize: '1.8vh',
              marginTop: '3px',
            }}
          >
            <span className={classes.showing}>Showing results for</span>{' '}
            {navigation.cards.adv.map((i, index) => {
              return (
                <span>
                  <span className={classes.category}>
                    {navigation.cards.adv[index]}
                  </span>{' '}
                  <span className={classes.search}>
                    '{navigation.cards.advSearch[index]}'
                  </span>
                  <span>
                    {navigation.cards.advRelation &&
                      navigation.cards.advRelation[index]}
                  </span>
                </span>
              );
            })}
            {/*  {navigation.cards.advValue.split(',').join('')} */}
          </Typography>
        ) : null}
        {navigation && navigation.cards.text ? (
          <Typography>
            {navigation.cards.text}
          </Typography>
        ) : null}
      </Breadcrumbs>
    </div>
  );
};

export { CustomSearchBreadcrumbs };
