import React, { Fragment, useContext } from 'react';
//material ui imports
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
//other components
import BarGraphStyles from '../styles/BarGraph.styles';
import PlusIcon from '../../../Images/svgs/plus';
import CustomBarGraph from '../../CustomComponents/Graphs/CustomBarGraph';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';

const BarGraph = ({ data }) => {
  const classes = BarGraphStyles();
  const { state, dispatch } = useContext(Store);

  const addGraphByYear = async () => {
    await dispatch({ type: Actions.SET_CART_ITEMS, value: state.cartItems + 1 });
    await dispatch({ type: Actions.SET_REPORT, value: { id: 'Bar Graph', data: { title: 'Biologics', subtitle: 'Immunogenicity - Approval by Year', searchTerm: state.searchTerm ? state.searchTerm.searchTerm : '', color: '#0E5A94', graph: data && data, keys: ["count"], index: "year" } } })
  }
  return (
    <Fragment>
      <Box display="flex" alignItems="center" justifyContent="space-between" className={classes.root}>
        <Typography className={classes.headings}>Approval by Year</Typography>
        <Typography onClick={addGraphByYear} className={classes.add}>
          <PlusIcon /> Add to report
        </Typography>
      </Box>
      <Box display="flex" className={classes.container}>
        {data && data ? (
          <CustomBarGraph keys={["count"]} colors={["#0E5A94"]} index="year" margin={{ top: 50, right: 0, bottom: 50, left: 60 }} data={data && data} />
        ) :
          (
            <Skeleton variant="rect" height="90%" width="100%" />
          )
        }
      </Box>
    </Fragment>
  );
}

export default BarGraph;