import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import DownloadLogo from '../../../../Images/download.svg';
import NotLike from '../../../../Images/NotLike.svg';
import NotDislike from '../../../../Images/NotDislike.svg';
import Like from '../../../../Images/Like.svg';
import Dislike from '../../../../Images/Dislike.svg';
import SuggestedAnswerStyles from './SuggestedAnswer.styles';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import { getFeedbackOnSuggestedAns } from '../../../ServiceApi/services';

export default function SuggestedAnswer(props) {
  const classes = SuggestedAnswerStyles();

  const [like, setLike] = useState([false, false]);

  useEffect(() => {
    try {
    } catch (e) {}
  }, [props]);

  const handleSetLikeButton = async (queryId, resultId, value) => {
    if (value === 'RELEVANT') {
      setLike([true, false]);
      var Base64 = require('js-base64').Base64;
      const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
      const hash = Base64.encode(tok);
      const Basic = 'Basic ' + hash;
      let requestOptions = {
        'query-id': queryId,
        'result-id': resultId,
        'user-reaction': 'RELEVANT',
      };
      // let response = await axios.post(
      //   'https://ria-api.vivpro.ai:8443/v2/clignosis/bot/submit_feedback',
      //   // 'http://localhost:5000/ria/bot/submit_feedback',
      //   requestOptions,
      //   { headers: { Authorization: Basic } }
      // );
      // if(like[0]===false) setLike([true,false])
      // else setLike([false,false])
      let response;
      await getFeedbackOnSuggestedAns(requestOptions)
        .then((res) => {
          response = res;
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      setLike([false, true]);
      var Base64 = require('js-base64').Base64;
      const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
      const hash = Base64.encode(tok);
      const Basic = 'Basic ' + hash;
      let requestOptions = {
        'query-id': queryId,
        'result-id': resultId,
        'user-reaction': 'NOT_RELEVANT',
      };
      // let response = await axios.post(
      //   'https://ria-api.vivpro.ai:8443/v2/clignosis/bot/submit_feedback',
      //   // 'http://localhost:5000/ria/bot/submit_feedback',
      //   requestOptions,
      //   { headers: { Authorization: Basic } }
      // );
      // if(like[1]===false) setLike([false,true])
      // else setLike([false,false])
      let response;
      await getFeedbackOnSuggestedAns(requestOptions)
        .then((res) => {
          response = res;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  return (
    <Grid
      item
      container
      xs={12}
      justify="flex-start"
      alignItems="center"
      className={classes.SuggestedAnswerGrid}
    >
      {/* <Grid
                                container
                                justify="space-between"
                                alignItems="center"
                              > */}
      <Grid item xs={10} justify="flex-start" alignItems="center">
        <Typography className={classes.ResponseText}>
          {props.suggestedAns ? props.ans.text : null}
        </Typography>
      </Grid>
      <Grid item xs={1} justify="flex-end" alignItems="center">
        <Box
          display="flex"
          justifyContent="center"
          className={classes.BotLikeButtonBox}
        >
          <IconButton
            className={classes.BotLikeButton}
            onClick={() =>
              handleSetLikeButton(
                props.ans.queryId,
                props.ans.resultId,
                'RELEVANT'
              )
            }
          >
            <img src={like[0] ? Like : NotLike}></img>
          </IconButton>
          <IconButton
            className={classes.BotLikeButton}
            onClick={() =>
              handleSetLikeButton(
                props.ans.queryId,
                props.ans.resultId,
                'NOT_RELEVANT'
              )
            }
          >
            <img src={like[1] ? Dislike : NotDislike}></img>
          </IconButton>
        </Box>
      </Grid>
      <Grid item xs={1} justify="flex-end" alignItems="center">
        <IconButton
          aria-label="show 0"
          color="primary"
          className={classes.SuggestedIconButton}
          onClick={() => window.open(props.ans.documentURL, '_blank')}
        >
          <img src={DownloadLogo}></img>
        </IconButton>
      </Grid>
      {/* </Grid> */}
    </Grid>
  );
}
