import React , {useState,useEffect} from 'react';
import FilterStyles from './Filters.styles'
import AutoComplete from './AutoCompleteComponent'
import ToggleFilter from './ToggleFilter'
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import { Typography } from '@material-ui/core';
import RangeFilter from './RangeFilter'
import {HeadingTypography} from '../../CustomComponents/Typography'
import Divider from '@material-ui/core/Divider';

export default function Filters(props) {


  const classes = FilterStyles();

  // const [props.filters,setFilters] = useState(props.props.filters)
  const [loading,setLoading] = useState(false)

  let tempFilterCounts =     {
    "activeIngredient" : {},
    "submissions" : {},
    "applications" : {},
    "formulation" : {},
    "route" : {},
    "sponsor" : {}
  }


  for (let i = 0; i < props.dataRows.length; i++) {
    if(props.dataRows[i]['activeIngredient'] in tempFilterCounts["activeIngredient"]){
        tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] = tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] + 1 } 
    else{tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] = 1}
    if (props.dataRows[i]['submissions'] in tempFilterCounts["submissions"])
      {tempFilterCounts["submissions"][props.dataRows[i]['submissions']] = tempFilterCounts["submissions"][props.dataRows[i]['submissions']] + 1 } 
    else
      {tempFilterCounts["submissions"][props.dataRows[i]['submissions']] = 1 } 

    if (props.dataRows[i]['applications'] in tempFilterCounts["applications"])
      { tempFilterCounts["applications"][props.dataRows[i]['applications']] = tempFilterCounts["applications"][props.dataRows[i]['applications']] + 1 } 
    else
      { tempFilterCounts["applications"][props.dataRows[i]['applications']] = 1 }   
      
    if (props.dataRows[i]['formulation'] in tempFilterCounts["formulation"])
      { tempFilterCounts["formulation"][props.dataRows[i]['formulation']] = tempFilterCounts["formulation"][props.dataRows[i]['formulation']] + 1 } 
    else
      { tempFilterCounts["formulation"][props.dataRows[i]['formulation']] = 1 } 
      
    if (props.dataRows[i]['route'] in tempFilterCounts["route"])
      { tempFilterCounts["route"][props.dataRows[i]['route']] = tempFilterCounts["route"][props.dataRows[i]['route']] + 1 } 
    else
      { tempFilterCounts["route"][props.dataRows[i]['route']] = 1 }
      if (props.dataRows[i]['sponsor'] in tempFilterCounts["sponsor"])
      { tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] = tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] + 1 } 
    else
      { tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] = 1 }
    }
    

  const [filterCounts,setFilterCounts] =  useState(tempFilterCounts)

useEffect(() => {
    async function getData() {
    try
    {
      let tempFilterCounts =     {
        "activeIngredient" : {},
        "submissions" : {},
        "applications" : {},
        "formulation" : {},
        "route" : {},
        "sponsor" : {}
      }


      for (let i = 0; i < props.dataRows.length; i++) {
        if(props.dataRows[i]['activeIngredient'] in tempFilterCounts["activeIngredient"]){
            tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] = tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] + 1 } 
        else{tempFilterCounts["activeIngredient"][props.dataRows[i]['activeIngredient']] = 1}
        if (props.dataRows[i]['submissions'] in tempFilterCounts["submissions"])
          {tempFilterCounts["submissions"][props.dataRows[i]['submissions']] = tempFilterCounts["submissions"][props.dataRows[i]['submissions']] + 1 } 
        else
          {tempFilterCounts["submissions"][props.dataRows[i]['submissions']] = 1 } 
    
        if (props.dataRows[i]['applications'] in tempFilterCounts["applications"])
          { tempFilterCounts["applications"][props.dataRows[i]['applications']] = tempFilterCounts["applications"][props.dataRows[i]['applications']] + 1 } 
        else
          { tempFilterCounts["applications"][props.dataRows[i]['applications']] = 1 }   
          
        if (props.dataRows[i]['formulation'] in tempFilterCounts["formulation"])
          { tempFilterCounts["formulation"][props.dataRows[i]['formulation']] = tempFilterCounts["formulation"][props.dataRows[i]['formulation']] + 1 } 
        else
          { tempFilterCounts["formulation"][props.dataRows[i]['formulation']] = 1 } 
          
        if (props.dataRows[i]['route'] in tempFilterCounts["route"])
          { tempFilterCounts["route"][props.dataRows[i]['route']] = tempFilterCounts["route"][props.dataRows[i]['route']] + 1 } 
        else
          { tempFilterCounts["route"][props.dataRows[i]['route']] = 1 }
        if (props.dataRows[i]['sponsor'] in tempFilterCounts["sponsor"])
        { tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] = tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] + 1 } 
        else
          { tempFilterCounts["sponsor"][props.dataRows[i]['sponsor']] = 1 }
        }

      setFilterCounts(tempFilterCounts)
    }
    catch(error)
    {
      console.log(error)
    }
  }
  getData();
}, [props,loading]);


return (
  <div className={classes.root}>
    <Box display="flex" justifyContent="space-between">
      {/* <HeadingTypography text={"Filters"}/> */}
      <Typography className={classes.FilterText}>Filters</Typography>
      <Button onClick={props.handleResetFilters} className={classes.ResetButton}>Reset</Button>
    </Box>
      <AutoComplete
        title = { String(props.filters.activeIngredient.length) === String(props.baseFilters.activeIngredient.length) || props.filters.activeIngredient.length === 0?
                 "Active Ingredient"+" ("+ String(props.baseFilters.activeIngredient.length) + ")"
                 :"Active Ingredient"+" ("+ String(props.filters.activeIngredient.length) + "/" + String(props.baseFilters.activeIngredient.length) + ")"}
        selected = {props.filters.activeIngredient}
        handleChange={(value)=>{props.functionFilter("activeIngredient",value)}}
        options={props.baseFilters.activeIngredient}
        placeholder = "Typer here ..."
        filterCount = {filterCounts.activeIngredient}
      />
       <Divider className={classes.Divider} variant="middle" />
      <AutoComplete
      title = { String(props.filters.formulation.length) === String(props.baseFilters.formulation.length) || props.filters.formulation.length === 0?
        "Formulation "+" ("+ String(props.baseFilters.formulation.length) + ")"
        :"Formulation "+" ("+ String(props.filters.formulation.length) + "/" + String(props.baseFilters.formulation.length) + ")"}
      selected = {props.filters.formulation}
      handleChange={(value)=>{props.functionFilter("formulation",value)}}
      options={props.baseFilters.formulation}
      placeholder = "Typer here ..."
      filterCount = {filterCounts.formulation}
    />
     <Divider className={classes.Divider} variant="middle" />
    <AutoComplete
      title = { String(props.filters.route.length) === String(props.baseFilters.route.length) || props.filters.route.length === 0?
        "Route "+" ("+ String(props.baseFilters.route.length) + ")"
        :"Route "+" ("+ String(props.filters.route.length) + "/" + String(props.baseFilters.route.length) + ")"}
      selected = {props.filters.route}
      handleChange={(value)=>{props.functionFilter("route",value)}}
      options={props.baseFilters.route}
      placeholder = "Typer here ..."
      filterCount = {filterCounts.route}
    />
     <Divider className={classes.Divider} variant="middle" />
    <RangeFilter 
        title = "Approval Date"
        selected = {props.filters.submissionDate}
        handleChange={(value)=>{
          props.functionFilter("submissionDate",value)
        }}
        options={props.baseFilters.submissionDate}
    />
    <Divider className={classes.Divider} variant="middle" />
    <AutoComplete
        title = { String(props.filters.sponsor.length) === String(props.baseFilters.sponsor.length) || props.filters.sponsor.length === 0?
                 "Sponsor Name"+" ("+ String(props.baseFilters.sponsor.length) + ")"
                 :"Sponsor Name"+" ("+ String(props.filters.sponsor.length) + "/" + String(props.baseFilters.sponsor.length) + ")"}
        selected = {props.filters.sponsor}
        handleChange={(value)=>{props.functionFilter("sponsor",value)}}
        options={props.baseFilters.sponsor}
        placeholder = "Typer here ..."
        filterCount = {filterCounts.sponsor}
      />
      <Divider className={classes.Divider} variant="middle" />
     <Divider className={classes.Divider} variant="middle" />
      <AutoComplete
        title = { String(props.filters.applications.length) === String(props.baseFilters.applications.length) || props.filters.applications.length === 0?
          "Application "+" ("+ String(props.baseFilters.applications.length) + ")"
          :"Application "+" ("+ String(props.filters.applications.length) + "/" + String(props.baseFilters.applications.length) + ")"}
        selected = {props.filters.applications}
        handleChange={(value)=>{props.functionFilter("applications",value)}}
        options={props.baseFilters.applications}
        placeholder = "Typer here ..."
        filterCount = {filterCounts.applications}
      />
       <Divider className={classes.Divider} variant="middle" />
      <AutoComplete
        title = { String(props.filters.submissions.length) === String(props.baseFilters.submissions.length) || props.filters.submissions.length === 0?
          "Submission "+" ("+ String(props.baseFilters.submissions.length) + ")"
          :"Submission "+" ("+ String(props.filters.submissions.length) + "/" + String(props.baseFilters.submissions.length) + ")"}
        selected = {props.filters.submissions}
        handleChange={(value)=>{props.functionFilter("submissions",value)}}
        options={props.baseFilters.submissions}
        placeholder = "Typer here ..."
        filterCount = {filterCounts.submissions}
      />
       <Divider className={classes.Divider} variant="middle" />
    </div>
  );
}

