import React, { useState, useEffect } from 'react';
import data from './data.json';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import ApplicationContentStyles from './ApplicationContent.styles';
import clsx from 'clsx';
import AA from '../../Images/aa.svg';
import BT from '../../Images/bt.svg';
import FT from '../../Images/ft.svg';
import CR from '../../Images/CR.svg';
import P from '../../Images/P.svg';
import S from '../../Images/S.svg';
import or from '../../Images/or.svg';
import Avatar from '@material-ui/core/Avatar';
import { CallReceived } from '@material-ui/icons';
import { objectEach } from 'highcharts';
import Tooltip from '@material-ui/core/Tooltip';
import Zoom from '@material-ui/core/Zoom';

const AdminStory = (props) => {
  const classes = ApplicationContentStyles();
  console.log('Admin story', props.AdminStoryRes);

  // let AdminstoryLength = props.AdminStoryRes.length;
  // console.log('Admin story length', props.AdminStoryRes.length);

  const CallCR = (value) => {
    console.log('call cr 1', value);

    if ('CR_Letter' in value) {
      if (value == 'Yes') {
        console.log('call cr 2', value);
        return (
          <Avatar alt="Logo" src={CR} className={classes.CardTopLogosYes} />
        );
      } else {
        return (
          <Avatar alt="Logo" src={CR} className={classes.CardTopLogosNo} />
        );
      }
    }
  };

  const callAdminIcon = (item) => {
    return Object.keys(item).map((key, index) => {
      if (
        key == 'Orphan_status' ||
        key == 'CR_Letter' ||
        key == 'Fast_track' ||
        key == 'Accelerated_Approval' ||
        key == 'Review_Priority' ||
        key == 'Breakthrough_designation'
      ) {
        if (key == 'CR_Letter') {
          if (item[key] == 'Yes') {
            return (
              <Tooltip TransitionComponent={Zoom} title={'CR Letter: ' + 'Yes'}>
                <Avatar
                  alt="Logo"
                  src={CR}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip TransitionComponent={Zoom} title={'CR Letter: ' + 'No'}>
                <Avatar
                  alt="Logo"
                  src={CR}
                  className={classes.CardTopLogosNo}
                />
              </Tooltip>
            );
          }
        }

        if (key == 'Orphan_status') {
          if (item[key] == 'Yes') {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Orphan status: ' + 'Yes'}
              >
                <Avatar
                  alt="Logo"
                  src={or}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Orphan status: ' + 'No'}
              >
                <Avatar
                  alt="Logo"
                  src={or}
                  className={classes.CardTopLogosNo}
                />
              </Tooltip>
            );
          }
        }
        if (key == 'Fast_track') {
          if (item[key] == 'Yes') {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Fast Track: ' + 'Yes'}
              >
                <Avatar
                  alt="Logo"
                  src={FT}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip TransitionComponent={Zoom} title={'Fast Track: ' + 'No'}>
                <Avatar
                  alt="Logo"
                  src={FT}
                  className={classes.CardTopLogosNo}
                />
              </Tooltip>
            );
          }
        }
        if (key == 'Accelerated_Approval') {
          if (item[key] == 'Yes') {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Accelerated Approval: ' + 'Yes'}
              >
                <Avatar
                  alt="Logo"
                  src={AA}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Accelerated Approval: ' + 'No'}
              >
                <Avatar
                  alt="Logo"
                  src={AA}
                  className={classes.CardTopLogosNo}
                />
              </Tooltip>
            );
          }
        }
        if (key == 'Review_Priority') {
          if (item[key] == 'STANDARD') {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Review Priority: ' + 'STANDARD'}
              >
                <Avatar
                  alt="Logo"
                  src={S}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Review Priority: ' + 'PRIORITY'}
              >
                <Avatar alt="Logo" src={P} className={classes.CardTopLogosNo} />
              </Tooltip>
            );
          }
        }
        if (key == 'Breakthrough_designation') {
          if (item[key] == 'Yes') {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Breakthrough designation: ' + 'Yes'}
              >
                <Avatar
                  alt="Logo"
                  src={BT}
                  className={classes.CardTopLogosYes}
                />
              </Tooltip>
            );
          } else {
            return (
              <Tooltip
                TransitionComponent={Zoom}
                title={'Breakthrough designation: ' + 'No'}
              >
                <Avatar
                  alt="Logo"
                  src={BT}
                  className={classes.CardTopLogosNo}
                />
              </Tooltip>
            );
          }
        }
      }
    });
  };

  return (
    <Grid container justify="center" item xs={12}>
      {props.AdminStoryRes ? (
        <div className={classes.adminStoryRow}>
          <Grid
            container
            justify="flex-start"
            item
            xs={3}
            style={{ flexWrap: 'inherit' }}
          >
            {props.AdminStoryRes.map((item) => {
              return callAdminIcon(item);
            })}
          </Grid>
        </div>
      ) : (
        <div></div>
      )}
    </Grid>
  );
};
export default AdminStory;
