import React, { useState, useEffect } from 'react';
import data from './data.json';
import ApplicationContentStyles from './ApplicationContent.styles';
import Grid from '@material-ui/core/Grid';
import GaugeChart from 'react-gauge-chart';
import ReactSpeedometer from 'react-d3-speedometer';
import { ResponsiveContainer } from 'recharts';

const TimeOfApproval = (props) => {
  const classes = ApplicationContentStyles();
  console.log('Time of Approval', props.TimeForApprovalRes);
  return (
    <Grid>
      <h5>Time for Approval (No. of days)</h5>

      {props.TimeForApprovalRes &&
      props.TimeForApprovalRes != 'N/A' &&
      props.TimeForApprovalRes != 'NA' ? (
        <ReactSpeedometer
          width={200}
          height={150}
          maxValue={
            props.TimeForApprovalRes > 1095 ? props.TimeForApprovalRes : 1095
          }
          value={props.TimeForApprovalRes}
          margin={{
            top: 15,
            right: 25,
            left: 5,
            bottom: 5,
          }}
          needleColor="#000000"
          customSegmentStops={[
            0,
            365,
            730,
            props.TimeForApprovalRes > 1095 ? props.TimeForApprovalRes : 1095,
          ]}
          segmentColors={['#66ff66', ' #ffff66', '	#FF4500']}
        />
      ) : (
        <div>Capture in Progress</div>
      )}
    </Grid>
  );
};

export default TimeOfApproval;
