import React, { useState, useEffect, useContext } from "react";
import { useHistory, Redirect } from "react-router-dom";
import { Auth } from "aws-amplify";
//material ui imports
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Box from "@material-ui/core/Box";
import Link from "@material-ui/core/Link";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import IconButton from "@material-ui/core/IconButton";
import Divider from "@material-ui/core/Divider";
// icons
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import AccountBoxOutlinedIcon from "@material-ui/icons/AccountBoxOutlined";
import VerifiedUserIcon from "@material-ui/icons/VerifiedUser";
// context
import Store from "../../../store";
import Actions from "../../../store/actions";
// other components
import logoWhite from "../../../assets/svgs/logoWhite.svg";
import NavbarStyles from "../styles/Navbar.styles";

const Navbar = () => {
  const classes = NavbarStyles();
  const { state, dispatch } = useContext(Store);
  const history = useHistory();
  const [user, setUser] = useState();
  const [anchorEl, setAnchorEl] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const user = await Auth.currentUserInfo();
        await dispatch({
          type: Actions.SET_MODULE,
          value: 'admin',
        });
        console.log(user);
        if (!user) {
          history.push("/login");
        } else {
          await dispatch({
            type: Actions.SET_USER_STUB,
            value: user.attributes,
          });
        }
        setUser(user.attributes);

      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = async () => {
    try {
      await Auth.signOut();
      await dispatch({ type: Actions.SET_USER, value: null });
      history.push("/");
    } catch (error) {
      console.log("error signing out: ", error);
    }
  };
  
  return (
    <AppBar elevation={3} position="fixed" className={classes.root}>
      <Container maxWidth="lg" className={classes.container}>
        <Toolbar>
          <Link href="/admin">
            <Box className={classes.responsiveLogo}>
              <img src={logoWhite} className={classes.vivLogo} alt="logo" />
            </Box>
          </Link>
        </Toolbar>
        <Toolbar>
          <Box
            display="flex"
            justifyContent="space-evenly"
            className={classes.linkBox}
          >
            {/* <Link underline="none" href="/admin">
              <Typography className={classes.navLinks}>Dashboard</Typography>
            </Link> */}
            <Link underline="none" href="/admin/reviews">
              <Typography className={classes.navLinks}>
                Data Curation
              </Typography>
            </Link>
          </Box>
        </Toolbar>
        <Toolbar>
          {state.userStub && state.userStub ? (
            <>
              <IconButton onClick={handleClick}>
                <AccountCircleIcon className={classes.navIcon} />
              </IconButton>
              <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                getContentAnchorEl={null}
                keepMounted
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "center",
                }}
                transformOrigin={{
                  vertical: "top",
                  horizontal: "center",
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
              >
                <MenuItem>
                  <ListItemIcon>
                    <AccountBoxOutlinedIcon
                      className={classes.menuIcon}
                      color="primary"
                      fontSize="small"
                    />
                  </ListItemIcon>
                  <Typography className={classes.username}>
                    {state.userStub && state.userStub["custom:user"]}
                  </Typography>
                </MenuItem>
                <MenuItem>
                  <ListItemIcon>
                    <VerifiedUserIcon
                      className={classes.menuIcon}
                      color="primary"
                      fontSize="small"
                    />
                  </ListItemIcon>
                  <Typography className={classes.username}>
                    {state.userStub && state.userStub["custom:role"]}
                  </Typography>
                </MenuItem>
                <Divider />
                <MenuItem button onClick={handleLogout}>
                  <ListItemIcon>
                    <ExitToAppIcon
                      className={classes.menuIcon}
                      fontSize="large"
                    />
                  </ListItemIcon>
                  <ListItemText className={classes.menuText}>
                    Logout
                  </ListItemText>
                </MenuItem>
                <MenuItem></MenuItem>
              </Menu>
            </>
          ) : null}
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Navbar;
