import React , {useState} from 'react';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import ToggleFilterStyles from './ToggleFilter.styles'
import Typography from '@material-ui/core/Typography';
import Collapse from "@material-ui/core/Collapse";
import Box from '@material-ui/core/Box';
import AddIcon from '@material-ui/icons/Add';

export default function ToggleFilter(props) {
  const classes = ToggleFilterStyles();
  const [filterExpanded, setFilterExpanded] = useState(false);
  const handleFilterChange = (value) => {
    props.handleChange(props.filterType,value)
  }

  return (
    <div className={classes.root}>
      <Box  display="flex" justifyContent="space-between"
      onClick={() => {
        if (filterExpanded) setFilterExpanded(false);
        else setFilterExpanded(true);
      }}
      style={{cursor:'pointer'}}>
      <Typography className = {classes.FilterTitle}
      aria-expanded={filterExpanded}
      >
          {props.title}
      </Typography>
      <AddIcon className = {classes.FilterTitleButton}/>
      </Box>
      <Collapse in={filterExpanded} timeout="auto" unmountOnExit>
      <ToggleButtonGroup
            exclusive
            name = {props.filterType}
            onChange={(event, value) => {
              handleFilterChange(value);
            }}
            aria-label="text alignment"
            value={props.selected}
            className={classes.toggleButtonGroup}
          >
            <ToggleButton
              value={["Yes"]}
              aria-label="YES"
              className={classes.toggleButtonIndivdual}
            >
              YES
            </ToggleButton>
            <ToggleButton
              value={["No"]}
              aria-label="NO"
              className={classes.toggleButtonIndivdual}
            >
              NO
            </ToggleButton>
            <ToggleButton
              value={["Yes","No"]}
              aria-label="ALL"
              className={classes.toggleButtonIndivdual}
            >
              ALL
            </ToggleButton>
          </ToggleButtonGroup>
      </Collapse>
    </div>
  );
}
