import React , { useEffect ,useState} from "react";
import ProductTableStyles from './ProductTable.styles'
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';

import RLD from '../../../Images/RLD.png'
import RS from '../../../Images/RS.png'
import TeCode from '../../../Images/TE.png'


export default function ProductTable(props) {
  const classes = ProductTableStyles();

  const [productData , setProductData] = useState()

  useEffect(() => {
		async function getData() {
			try {
           setProductData(props.products)     
			} catch (e) {
				console.log(e)
			}
		}
		getData();
    }, [props.products])

  if(productData){
  return (
    <Paper className={classes.Paper}>
      <Table className={classes.Table} aria-label="customized table">
                      <TableHead className={classes.TableHead}>
                        <TableRow className={classes.StyledHeaderTableRow} >
                          <TableCell className={classes.StyledHeaderTableCell}>Products</TableCell>
                          {Object.keys(productData).map((key,index) => {
                                  return(
                                          <TableCell className={classes.StyledHeaderTableCellMapped}>Product {key}</TableCell>
                                  )
                                }
                          )}
                        </TableRow>
                      </TableHead>
                      <TableBody className={classes.TableBody}>
                        <TableRow className={classes.StyledTableRow} key="Strength">
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                              Reference Status
                            </TableCell>
                            {Object.keys(productData).map((key,index) => {
                                  return(
                                          <TableCell className={classes.StyledTableCellMapped}>
                                            <div className={classes.StyledLogosCellDiv}>
                                                <Grid xs={12} container justify="center" alignItems="center">
                                                        <Grid container justify="center" alignItems="center" item xs={4}>
                                                            {productData[key]["rld"] === "YES"?
                                                            <Avatar alt="Logo" src={RLD} className={classes.RefernceStatusLogosYes}/>
                                                            :
                                                            <Avatar alt="Logo" src={RLD} className={classes.RefernceStatusLogosNo}/>}
                                                        </Grid>
                                                        <Grid container justify="center" alignItems="center" item xs={4}>
                                                            {productData[key]["rs"] === "YES"?
                                                            <Avatar alt="Logo" src={RS} className={classes.RefernceStatusLogosYes}/>
                                                            :
                                                            <Avatar alt="Logo" src={RS} className={classes.RefernceStatusLogosNo}/>}
                                                        </Grid>
                                                        <Grid container justify="center" alignItems="center" item xs={4}>
                                                            {productData[key]["teCode"] !== "None"?
                                                            <Avatar alt="Logo" src={TeCode} className={classes.RefernceStatusLogosYes}/>
                                                            :
                                                            <Avatar alt="Logo" src={TeCode} className={classes.RefernceStatusLogosNo}/>}
                                                        </Grid>
                                              </Grid>
                                              </div>
                                          </TableCell>
                                  )
                                }
                          )}
                          </TableRow>
                        <TableRow className={classes.StyledTableRow} key="Strength">
                              <TableCell className={classes.StyledTableCell} component="th" scope="row">
                                Strength
                              </TableCell>
                              {Object.keys(productData).map((key,index) => {
                                    return(
                                            <TableCell className={classes.StyledTableCellMapped}>{productData[key]["Strength"]}</TableCell>
                                    )
                                  }
                            )}
                            </TableRow>
                          <TableRow className={classes.StyledTableRow} key="Marketing">
                            <TableCell className={classes.StyledTableCell} component="th" scope="row">
                              Marketing Status
                            </TableCell>
                            {Object.keys(productData).map((key,index) => {
                                  return(
                                          <TableCell className={classes.StyledTableCellMapped}>{productData[key]["marketingstatus"]}</TableCell>
                                  )
                                }
                          )}
                          </TableRow>
                      </TableBody>
        </Table>
    </Paper>
    )
  }
  else{
    return(
      <div></div>
    )
  }
}