import React, { Fragment } from 'react';
//external libs
import { ResponsivePie } from '@nivo/pie'
//style
import CustomPieChartStyles from './CustomPieChart.styles';

const CustomPieChart = ({data, margin }) => {
  const classes = CustomPieChartStyles();
  return (
    <Fragment>
      <ResponsivePie
        className={classes.root}
        data={data}
        margin={margin || { top: 40, right: 40, bottom: 20, left: 30 }}
        innerRadius={0.2}
        padAngle={0.7}
        cornerRadius={0}
        startAngle={-50}
        // colors={['#40C4FF', '#0E83DC']}
        colors={['#48E0A6', '#0A467C','#008B8B','#318AC8']}
        // sliceLabel={function (e) { return e.id + "(" + e.value + "%)" }}
        sliceLabel={function (e) { return e.value }}
        borderWidth={1}
        borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
        // enableRadialLabels={false}
        radialLabelsLinkOffset={-10}
        radialLabelsLinkDiagonalLength={20}
        radialLabelsLinkHorizontalLength={10}
        radialLabelsSkipAngle={10}
        radialLabelsTextColor="#1FBDCA"
        radialLabelsLinkColor={{ from: 'color' }}
        sliceLabelsSkipAngle={10}
        sliceLabelsTextColor="#fff"
        theme={{
          axis: {
            fontSize: "14px",
            tickColor: "#000",
            ticks: {
              line: {
                stroke: "#555555"
              },
              text: {
                fill: "#1FBDCA",
                fontWeight: 'bold'
              }
            }
          },
        }}
      />
    </Fragment>
  );
}

export default CustomPieChart;