import React, { useContext, useState, useEffect, Fragment } from 'react';
import Toolbar from '@material-ui/core/Toolbar';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
// import Store from '../../store';
// import Actions from '../../store/actions';
import ARIA from '../../Bot';
import Grid from '@material-ui/core/Grid';
import PmrIcon from '../../../Images/svgs/pmrIcon';
import IconButton from '@material-ui/core/IconButton';
import lockcontentstyles from './lockcontent.styles';

const LockContent = (props) => {
  const classes = lockcontentstyles();
  const [lockContent, setLockContent] = useState();
  const [module, setModule] = useState();
  useEffect(() => {
    async function getData() {
      try {
        setModule(props.module);
        setLockContent(props.license);
      } catch (e) {}
    }
    getData();
  });
  return (
    <div>
      {console.log('hi', lockContent)}
      {lockContent ? (
        <Grid>
          {(lockContent === 'Evaluation' ||
            lockContent === 'Educational' ||
            lockContent === 'Free' ||
            lockContent === 'Free/Community') &&
          module === 'pmr' ? (
            <Grid>
              <IconButton edge="start" id="pmrIcon" style={{ zoom: 1.3 }}>
                <PmrIcon />
              </IconButton>
              <Grid>
                <Typography className={classes.MainText}>
                  You don't have permission to access PMR
                </Typography>
                <Typography className={classes.subtext}>
                  You can always contact us using the button below and we will
                  get back to you within 24 hours.
                </Typography>
                <Typography className={classes.remember}>
                  Remember - we're glad to have you with us!
                </Typography>
              </Grid>
            </Grid>
          ) : null}
        </Grid>
      ) : null}
    </div>
  );
};

export default LockContent;
