import React, { Fragment, useContext } from 'react';
//MATERIAL UI IMPORTS
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
// import Paper from '@material-ui/core/Paper';
//styles
import LineGraphStyles from '../styles/LineGraph.styles';
//other imports
import PlusIcon from '../../../Images/svgs/plus';
import CustomBarGraph from '../../CustomComponents/Graphs/CustomBarGraph';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';

const LineGraph = ({ data }) => {
  const classes = LineGraphStyles();
  const { state, dispatch } = useContext(Store);

  const addGraphByYear = async () => {
    await dispatch({ type: Actions.SET_CART_ITEMS, value: state.cartItems + 1 });
    await dispatch({ type: Actions.SET_REPORT, value: { id: 'Bar Graph', data: { title: 'Biologics', subtitle: 'Business Intelligence - Approval by Year', graph: data && data.approvalYearGraph, keys: ["count"], index: "year", color: "#0E5A94" } } })
  }
  const addGraphByTime = async () => {
    await dispatch({ type: Actions.SET_CART_ITEMS, value: state.cartItems + 1 });
    await dispatch({ type: Actions.SET_REPORT, value: { id: 'Bar Graph', data: { title: 'Biologics', subtitle: 'Business Intelligence - Approvals Time, Days from Filling', graph: data && data.approvalTimeGraph, keys: ["count"], index: "year", color: "#008B8B" } } })
  }
  return (
    <Fragment>
      <Grid container lg={12}>
        <Grid item lg={6}>
          <Box
            display="flex"
            flexDirection="column"
            className={classes.graphBox}
          >
            {/* <Paper elevation={2} className={classes.paper}> */}
            <Box mr={5} display="flex" flexDirection="row" justifyContent="space-between">
              <Typography className={classes.headings}>
                Approval by Year
            </Typography>
              <Typography onClick={addGraphByYear} className={classes.add}>
                <PlusIcon /> Add to report
            </Typography>
            </Box>
            {data && data ? (
              <CustomBarGraph keys={["count"]} colors={["#0E5A94"]} index="year" data={data && data.approvalYearGraph} />
            ) : (
                <Skeleton variant="rect" height="90%" width="90%" />
              )}
            {/* </Paper> */}
          </Box>
        </Grid>
        <Grid item lg={6}>
          <Box
            display="flex"
            flexDirection="column"
            className={classes.graphBox}
          >
            {/* <Paper elevation={2} className={classes.paper}> */}
            <Box mr={5} display="flex" flexDirection="row" justifyContent="space-between">
              <Typography className={classes.headings}>
                Approvals Time, Days from Filling
            </Typography>
              <Typography onClick={addGraphByTime} className={classes.add}>
                <PlusIcon /> Add to report
            </Typography>
            </Box>
            {data && data ? (
              <CustomBarGraph className={classes.graph} keys={["count"]} colors={["#008B8B"]} index="year" data={data && data.approvalTimeGraph} />
            ) : (
                <Skeleton variant="rect" height="90%" width="90%" />
              )}
            {/* </Paper> */}
          </Box>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default LineGraph;
