import React, { useState, useEffect } from 'react';
import FilterStyles from './Filters.styles';
import AutoComplete from './AutoCompleteComponent';
// import RangeFilter from './RangeFilter'
import { UpdateRounded } from '@material-ui/icons';
import { HeadingTypography } from '../../CustomComponents/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';

export default function Filters(props) {
  const classes = FilterStyles();
  const [dataRows, setDataRows] = useState(props.data);
  console.log('Length', props.data.length);
  const [loading, setLoading] = useState(false);
  const [filterList, setFilterList] = useState({
    Interventions: [],
    Status: [],
    Phase: [],
    Sex: [],
    Age: [],
    Sponsor_Colalborators: [],
  });

  let tempFilterCounts = {
    Interventions: {},
    Status: {},
    Phase: {},
    Sex: {},
    Age: {},
    Sponsor_Colalborators: {},
  };

  for (let i = 0; i < dataRows.length; i++) {
    if (dataRows[i]['Interventions'] in tempFilterCounts['Interventions']) {
      tempFilterCounts['Interventions'][dataRows[i]['Interventions']] =
        tempFilterCounts['Interventions'][dataRows[i]['Interventions']] + 1;
    } else {
      tempFilterCounts['Interventions'][dataRows[i]['Interventions']] = 1;
    }

    if (dataRows[i]['status'] in tempFilterCounts['Status']) {
      tempFilterCounts['Status'][dataRows[i]['status']] =
        tempFilterCounts['Status'][dataRows[i]['status']] + 1;
    } else {
      tempFilterCounts['Status'][dataRows[i]['status']] = 1;
    }

    if (dataRows[i]['Phase'] in tempFilterCounts['Phase']) {
      tempFilterCounts['Phase'][dataRows[i]['Phase']] =
        tempFilterCounts['Phase'][dataRows[i]['Phase']] + 1;
    } else {
      tempFilterCounts['Phase'][dataRows[i]['Phase']] = 1;
    }

    if (dataRows[i]['Sex'] in tempFilterCounts['Sex']) {
      tempFilterCounts['Sex'][dataRows[i]['Sex']] =
        tempFilterCounts['Sex'][dataRows[i]['Sex']] + 1;
    } else {
      tempFilterCounts['Sex'][dataRows[i]['Sex']] = 1;
    }

    if (dataRows[i]['Age'] in tempFilterCounts['Age']) {
      tempFilterCounts['Age'][dataRows[i]['Age']] =
        tempFilterCounts['Age'][dataRows[i]['Age']] + 1;
    } else {
      tempFilterCounts['Age'][dataRows[i]['Age']] = 1;
    }
    if (
      dataRows[i]['Sponsor_Colalborators'] in
      tempFilterCounts['Sponsor_Colalborators']
    ) {
      tempFilterCounts['Sponsor_Colalborators'][
        dataRows[i]['Sponsor_Colalborators']
      ] =
        tempFilterCounts['Sponsor_Colalborators'][
          dataRows[i]['Sponsor_Colalborators']
        ] + 1;
    } else {
      tempFilterCounts['Sponsor_Colalborators'][
        dataRows[i]['Sponsor_Colalborators']
      ] = 1;
    }
  }

  const [filterCounts, setFilterCounts] = useState(tempFilterCounts);

  const [filters, setFilters] = useState(props.filters);
  const [resetFlag, setResetFlag] = useState(false);

  useEffect(() => {
    async function getData() {
      try {
        if (props.filters && !resetFlag) {
          setFilters(props.filters);
        } else {
          setFilters({
            Interventions: [],
            Status: [],
            Phase: [],
            Sex: [],
            Age: [],
            Sponsor_Colalborators: [],
          });
        }
        let baseFilterList = { ...filterList };
        for (let i = 0; i < dataRows.length; i++) {
          if (
            !baseFilterList['Interventions'].includes([
              dataRows[i]['Interventions'],
            ])
          ) {
            baseFilterList['Interventions'].push(dataRows[i]['Interventions']);
          }
          if (!baseFilterList['Status'].includes(dataRows[i]['status'])) {
            baseFilterList['Status'].push(dataRows[i]['status']);
          }
          if (!baseFilterList['Phase'].includes(dataRows[i]['Phase'])) {
            baseFilterList['Phase'].push(dataRows[i]['Phase']);
          }
          if (!baseFilterList['Sex'].includes(dataRows[i]['Sex'])) {
            baseFilterList['Sex'].push(dataRows[i]['Sex']);
          }

          if (!baseFilterList['Age'].includes(dataRows[i]['Age'])) {
            baseFilterList['Age'].push(dataRows[i]['Age']);
          }
          if (
            !baseFilterList['Sponsor_Colalborators'].includes(
              dataRows[i]['Sponsor_Colalborators']
            )
          ) {
            baseFilterList['Sponsor_Colalborators'].push(
              dataRows[i]['Sponsor_Colalborators']
            );
          }
        }

        for (let key in baseFilterList) {
          baseFilterList[key] = baseFilterList[key].filter(function (element) {
            return element !== null;
          });
        }

        for (let key in baseFilterList) {
          baseFilterList[key] = baseFilterList[key].filter(function (element) {
            return element !== undefined;
          });
        }

        setFilterList(baseFilterList);
        let updatedFilters = { ...filters };
        if (updatedFilters['Interventions'].length === 0)
          updatedFilters['Interventions'] = baseFilterList['Interventions'];
        if (updatedFilters['Status'].length === 0)
          updatedFilters['Status'] = baseFilterList['Status'];
        if (updatedFilters['Phase'].length === 0)
          updatedFilters['Phase'] = baseFilterList['Phase'];
        if (updatedFilters['Sex'].length === 0)
          updatedFilters['Sex'] = baseFilterList['Sex'];
        if (updatedFilters['Age'].length === 0)
          updatedFilters['Age'] = baseFilterList['Age'];
        if (updatedFilters['Sponsor_Colalborators'].length === 0)
          updatedFilters['Sponsor_Colalborators'] =
            baseFilterList['Sponsor_Colalborators'];
        let clinicalTrialsData = [];

        for (let key in updatedFilters) {
          updatedFilters[key] = updatedFilters[key].filter(function (element) {
            return element !== null;
          });
        }

        for (let key in updatedFilters) {
          updatedFilters[key] = updatedFilters[key].filter(function (element) {
            return element !== undefined;
          });
        }

        console.log(updatedFilters);

        let others = [];
        for (let i = 0; i < dataRows.length; i++) {
          if (
            updatedFilters['Interventions'].includes(
              dataRows[i]['Interventions']
            )
          ) {
            if (updatedFilters['Status'].includes(dataRows[i]['status'])) {
              if (updatedFilters['Phase'].includes(dataRows[i]['Phase'])) {
                if (updatedFilters['Sex'].includes(dataRows[i]['Sex'])) {
                  if (updatedFilters['Age'].includes(dataRows[i]['Age'])) {
                    if (
                      updatedFilters['Sponsor_Colalborators'].includes(
                        dataRows[i]['Sponsor_Colalborators']
                      )
                    ) {
                      clinicalTrialsData.push(dataRows[i]);
                    } else {
                      others.push(dataRows[i]);
                    }
                  } else {
                    others.push(dataRows[i]);
                  }
                } else {
                  others.push(dataRows[i]);
                }
              } else {
                others.push(dataRows[i]);
              }
            } else {
              others.push(dataRows[i]);
            }
          } else {
            others.push(dataRows[i]);
          }
        }
        props.handleFilterChange(filters);
        props.handleRowsChange(clinicalTrialsData);
        // console.log('ct length', clinicalTrialsData.length);
        console.log('others', others);
        let tempFilterCounts = {
          Interventions: {},
          Status: {},
          Phase: {},
          Sex: {},
          Age: {},
          Sponsor_Colalborators: {},
        };

        for (let i = 0; i < clinicalTrialsData.length; i++) {
          if (
            clinicalTrialsData[i]['Interventions'] in
            tempFilterCounts['Interventions']
          ) {
            tempFilterCounts['Interventions'][
              clinicalTrialsData[i]['Interventions']
            ] =
              tempFilterCounts['Interventions'][
                clinicalTrialsData[i]['Interventions']
              ] + 1;
          } else {
            tempFilterCounts['Interventions'][
              clinicalTrialsData[i]['Interventions']
            ] = 1;
          }

          if (clinicalTrialsData[i]['status'] in tempFilterCounts['Status']) {
            tempFilterCounts['Status'][clinicalTrialsData[i]['status']] =
              tempFilterCounts['Status'][clinicalTrialsData[i]['status']] + 1;
          } else {
            tempFilterCounts['Status'][clinicalTrialsData[i]['status']] = 1;
          }

          if (clinicalTrialsData[i]['Phase'] in tempFilterCounts['Phase']) {
            tempFilterCounts['Phase'][clinicalTrialsData[i]['Phase']] =
              tempFilterCounts['Phase'][clinicalTrialsData[i]['Phase']] + 1;
          } else {
            tempFilterCounts['Phase'][clinicalTrialsData[i]['Phase']] = 1;
          }

          if (clinicalTrialsData[i]['Sex'] in tempFilterCounts['Sex']) {
            tempFilterCounts['Sex'][clinicalTrialsData[i]['Sex']] =
              tempFilterCounts['Sex'][clinicalTrialsData[i]['Sex']] + 1;
          } else {
            tempFilterCounts['Sex'][clinicalTrialsData[i]['Sex']] = 1;
          }

          if (clinicalTrialsData[i]['Age'] in tempFilterCounts['Age']) {
            tempFilterCounts['Age'][clinicalTrialsData[i]['Age']] =
              tempFilterCounts['Age'][clinicalTrialsData[i]['Age']] + 1;
          } else {
            tempFilterCounts['Age'][clinicalTrialsData[i]['Age']] = 1;
          }
          if (
            clinicalTrialsData[i]['Sponsor_Colalborators'] in
            tempFilterCounts['Sponsor_Colalborators']
          ) {
            tempFilterCounts['Sponsor_Colalborators'][
              clinicalTrialsData[i]['Sponsor_Colalborators']
            ] =
              tempFilterCounts['Sponsor_Colalborators'][
                clinicalTrialsData[i]['Sponsor_Colalborators']
              ] + 1;
          } else {
            tempFilterCounts['Sponsor_Colalborators'][
              clinicalTrialsData[i]['Sponsor_Colalborators']
            ] = 1;
          }
        }

        setFilterCounts(tempFilterCounts);
      } catch (error) {
        console.log(error);
      }
    }
    getData();
  }, [loading]);

  const handleFilterChange = async (filterType, value) => {
    let newFilters = filters;
    newFilters[filterType] = value;
    setFilters(newFilters);

    if (!loading) setLoading(true);
    else setLoading(false);
  };

  const handleResetFilters = async () => {
    let resetFilters = {
      Interventions: [],
      Status: [],
      Phase: [],
      Sex: [],
      Age: [],
      Sponsor_Colalborators: [],
    };
    setFilters(resetFilters);
    setResetFlag(false);
    if (!loading) setLoading(true);
    else setLoading(false);
  };

  console.log(filters);

  return (
    <div className={classes.root}>
      <Box display="flex" justifyContent="space-between">
        {/* <HeadingTypography text={"Filters"}/> */}
        <Typography className={classes.FilterText}>Filters</Typography>
        <Button onClick={handleResetFilters} className={classes.ResetButton}>
          Reset
        </Button>
      </Box>
      <AutoComplete
        title={
          String(filters['Status'].length) ===
            String(filterList['Status'].length) ||
          filters['Status'].length === 0
            ? 'Status' + ' (' + String(filterList['Status'].length) + ')'
            : 'Status' +
              ' (' +
              String(filters['Status'].length) +
              '/' +
              String(filterList['Status'].length) +
              ')'
        }
        selected={filters['Status']}
        filterType="Status"
        handleChange={handleFilterChange}
        options={filterList['Status']}
        placeholder="Typer here ..."
        filterCount={filterCounts['Status']}
      />
      <AutoComplete
        title={
          String(filters['Phase'].length) ===
            String(filterList['Phase'].length) || filters['Phase'].length === 0
            ? 'Phase' + ' (' + String(filterList['Phase'].length) + ')'
            : 'Phase' +
              ' (' +
              String(filters['Phase'].length) +
              '/' +
              String(filterList['Phase'].length) +
              ')'
        }
        selected={filters['Phase']}
        filterType="Phase"
        handleChange={handleFilterChange}
        options={filterList['Phase']}
        placeholder="Typer here ..."
        filterCount={filterCounts['Phase']}
      />
      <AutoComplete
        title={
          String(filters['Sex'].length) === String(filterList['Sex'].length) ||
          filters['Sex'].length === 0
            ? 'Gender Description' +
              ' (' +
              String(filterList['Sex'].length) +
              ')'
            : 'Gender Description' +
              ' (' +
              String(filters['Sex'].length) +
              '/' +
              String(filterList['Sex'].length) +
              ')'
        }
        selected={filters['Sex']}
        filterType="Sex"
        handleChange={handleFilterChange}
        options={filterList['Sex']}
        placeholder="Typer here ..."
        filterCount={filterCounts['Sex']}
      />
      <AutoComplete
        title={
          String(filters['Age'].length) === String(filterList['Age'].length) ||
          filters['Age'].length === 0
            ? 'Age' + ' (' + String(filterList['Age'].length) + ')'
            : 'Age' +
              ' (' +
              String(filters['Age'].length) +
              '/' +
              String(filterList['Age'].length) +
              ')'
        }
        selected={filters['Age']}
        filterType="Age"
        handleChange={handleFilterChange}
        options={filterList['Age']}
        placeholder="Typer here ..."
        filterCount={filterCounts['Age']}
      />
    </div>
  );
}
