import React, { Fragment, useEffect, useState, useContext } from 'react';
//MATERIAL UI IMPORTS
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
//other components
import BiologicsStyles from '../styles/Biologics.styles';
import Cards from './Cards';
import LineGraph from './LineGraph';
import Percentages from './Percentages';
import PlusIcon from '../../../Images/svgs/plus';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';
import Base64 from 'js-base64';
import { getBLAstats } from '../../ServiceApi/services';

const BusinessIntelligence = ({
  accelerated_approval,
  fastrack,
  breakthrough_designation,
  yearGraph,
  activeIngredients,
  formulations,
  blaCount,
}) => {
  const classes = BiologicsStyles();
  const { state, dispatch } = useContext(Store);
  const [percentages, setPercentages] = useState();
  const [linegraph, setLineGraph] = useState();
  const [cards, setCards] = useState();

  useEffect(() => {
    async function getData() {
      try {
        var Base64 = require('js-base64').Base64;
        const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
        const hash = Base64.encode(tok);
        const Basic = 'Basic ' + hash;
        // await fetch(
        //   // 'https://ria-staging.vivpro.org:8443/v2/clignosis/bla_stats',
        //   // 'https://ria-api.vivpro.ai:8443/v2/clignosis/bla_stats',
        //   'https://ria-api-staging.vivpro.ai:8443/v2/clignosis/bla_stats',
        //   { headers: { Authorization: Basic } }
        // )
        //   .then((response) => response.json())
        let response;
        await getBLAstats()
          .then((res) => {
            response = res;
          })
          .catch((error) => {
            console.log(error);
          });
        // .then((data) => {
        const percentagesData = {
          acceleratedApproval:
            accelerated_approval ||
            response.data.Success.business_intelligence.Accelerated_Approval,
          breakthroughDesignation:
            breakthrough_designation ||
            response.data.Success.business_intelligence
              .Breakthrough_designation,
          fasttrack:
            fastrack || response.data.Success.business_intelligence.Fast_track,
          orphanStatus:
            response.data.Success.business_intelligence.Orphan_status,
        };
        setPercentages(percentagesData);
        let approvaltimeGraphData = [];
        for (let elem of response.data.Success.business_intelligence
          .approvaltimeGraph) {
          let year = elem.year;
          let count = parseInt(elem.count);
          approvaltimeGraphData.push({ count, year });
        }
        approvaltimeGraphData.sort(
          (a, b) => parseInt(a.year) - parseInt(b.year)
        );
        let approvalyearGraphData = [];
        for (let elem of yearGraph ||
          response.data.Success.business_intelligence.approvalyearGraph) {
          let year = elem.year;
          let count = parseInt(elem.count);
          approvalyearGraphData.push({ count, year });
        }
        approvalyearGraphData.sort(
          (a, b) => parseInt(a.year) - parseInt(b.year)
        );

        const lineGraphData = {
          approvalTimeGraph: approvaltimeGraphData,
          approvalYearGraph: approvalyearGraphData,
        };
        setLineGraph(lineGraphData);
        setCards([
          {
            id: 'BLA',
            count:
              blaCount || response.data.Success.business_intelligence.bla_count,
          },
          {
            id: 'Active Ingredients',
            count:
              activeIngredients ||
              response.data.Success.business_intelligence.activeIngredients,
          },
          {
            id: 'Formulations',
            count:
              formulations ||
              response.data.Success.business_intelligence.formulations,
          },
        ]);
        // });
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, []);

  const addProgressBars = async () => {
    await dispatch({
      type: Actions.SET_CART_ITEMS,
      value: state.cartItems + 1,
    });
    await dispatch({
      type: Actions.SET_REPORT,
      value: { id: 'Progress Bars', title: 'Biologics', data: percentages },
    });
  };
  return (
    <Fragment>
      <Grid container>
        <Grid item lg={2}>
          <Cards data={cards} />
        </Grid>
        <Grid item lg={10}>
          <Grid item lg={12}>
            <LineGraph data={linegraph} />
          </Grid>
          <Grid item lg={12}>
            <Box
              mr={2}
              display="flex"
              alignItems="center"
              justifyContent="flex-end"
              className={classes.percentages}
            >
              <Typography onClick={addProgressBars} className={classes.add}>
                <PlusIcon /> Add to report
              </Typography>
            </Box>
            <Percentages data={percentages} />
          </Grid>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default BusinessIntelligence;
