import React, { useContext, useState, useEffect } from 'react';
import HomeStyles from './Home.styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import Fab from '@material-ui/core/Fab';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import BotLogo from '../../Images/bot.svg';
import Bot from '../Bot';
import Graph from './Graph';
import LatestNews from './LatestNews';
//context store
import Store from '../../store';
import Actions from '../../store/actions';
import Carousel from 'react-material-ui-carousel';
//other components
import PlusIcon from '../../Images/svgs/plus';
//guide tour
import { Steps, Hints } from 'intro.js-react';
import 'intro.js/introjs.css';
import Airplane from '../../Images/svgs/airplane';

const BotTooltip = withStyles((theme) => ({
  tooltip: {
    marginTop: '-11vh',
    marginLeft: '-1vh',
    width: 'fit-content',
    padding: '10px',
    paddingLeft: '20px',
    paddingRight: '20px',
    borderRadius: '10px',
    color: theme.palette.white,
    border: `2px solid ${theme.palette.primary.lightBorder}`,
  },
}))(Tooltip);

export default function Home() {
  const classes = HomeStyles();
  const { state, dispatch } = useContext(Store);
  var maxNumber = 7;
  var randomNumber = Math.floor(Math.random() * maxNumber + 1);
  const [graphIndex, setGraphIndex] = useState(randomNumber);

  const [bot, setBot] = useState(false);
  const [tooltipIsOpen, setTooltipIsOpen] = useState(true);
  //guide tour state
  const [steps, setSteps] = useState([
    {
      element: '#coreIcon',
      intro: 'Core Module: This is a core RIA module to search for all Drugs, Biologics and Devices for covered areas',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#tabletIcon',
      intro:
        '505b2 Module: This module includes applications approved under 505(b)(2) approval pathway',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#pediatricIcon',
      intro: 'Pediatric Module: This module includes access pediatric written requests, waivers, and deferrals',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#blaIcon',
      intro:
        'BLA Module: This module includes Biologics Licensing Applications (BLAs)',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#pmrIcon',
      intro:
        'PMR Module: This module includes surveillance and impact for Post Marketing Requirements and Commitments',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#quickSearch',
      intro:
        'Quick Search: The user can search for a list of all drugs and brands by ‘TA/Indication’ or  ‘Active Ingredient’ or ‘Trade Name’ or ‘formulation’ or ‘route’ or ‘Approval Date’. The search process is as follows',
      position: 'bottom',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#filterTerm',
      intro:
        'select database (US FDA- Drugs and Biologics, EU EMA - Drugs and Biologics, US DA - Devices)',
      position: 'bottom',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#categoryTerm',
      intro:
        'select category (TA/Indication, Active Ingredient, Trade Name, Formulation, Route, Approval Date)',
      position: 'bottom',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#searchTerm',
      intro:
        'enter a search string and press enter or hit the search button.',
      position: 'bottom',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#advanceSearch',
      intro:
        'Advanced Search:  The user can search for different combinations the ‘TA/Indication’ or  ‘Active Ingredient’ or ‘Trade Name’ or ‘formulation’ or ‘route’ or ‘Approval Date’',
      position: 'bottom',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#homeInsights',
      intro:
        'Insights: Key business insights frequently asked questions from RIA database',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#homeNews',
      intro:
        'News: Recent FDA newsfeed for users to learn about the latest news',
      position: 'left',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    }
  ]);
  const [stepsEnabled, setStepsEnabled] = useState(false);

  const onExit = () => {
    setStepsEnabled(false);
  };
  const onBeforeChange = (nextStepIndex) => {
    if (nextStepIndex === 1) {
      // setTableExpanded(true);
    }
  }
  useEffect(() => {
    async function getData() {
      try {
        await dispatch({ type: Actions.SET_MODULE, value: 'core' });
        setTimeout(() => {
          setTooltipIsOpen(false);
        }, 5000);
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, [dispatch]);

  const addGraph = async () => {
    await dispatch({
      type: Actions.SET_CART_ITEMS,
      value: state.cartItems + 1,
    });
    await dispatch({
      type: Actions.SET_REPORT,
      value: { id: 'Home Report', data: { key: graphIndex } },
    });
  };
  const handleNext = () => {
    if (graphIndex < 7) {
      setGraphIndex(graphIndex + 1);
    } else {
      setGraphIndex(1);
    }
  };
  const handlePrev = () => {
    if (graphIndex <= 7) {
      if (graphIndex === 1) {
        setGraphIndex(7);
      } else {
        setGraphIndex(graphIndex - 1);
      }
    }
  };
  return (
    <div className={classes.main}>
      <Steps
        enabled={stepsEnabled}
        steps={steps}
        showProgress={true}
        scrollToElement={true}
        // ref={steps => (this.steps = steps)}
        initialStep={0}
        onBeforeChange={onBeforeChange}
        onExit={onExit}
      />
      <Grid container spacing={2} className={classes.GridGraphBox}>
        <Grid item xs={6} className={classes.GridGraph}>
          <Paper elevation={1} className={classes.paper}>
            <Box display="flex" justifyContent="space-between" id="homeInsights">
              <Typography> </Typography>
              <Typography variant="h1" className={classes.GridTitle}>
                Insights
              </Typography>
              <Typography onClick={addGraph} className={classes.add}>
                <PlusIcon /> Add to report
              </Typography>
            </Box>

            <Grid
              xs={12}
              item
              justify="center"
              alignItems="center"
              className={classes.GridContentBox}
            >
              <Carousel
                next={() => handleNext()}
                prev={() => handlePrev()}
                autoPlay={true}
                indicators="true"
                interval="20000"
                indicatorColor="primary"
                className={classes.GridContentBox}
              // style={{ height: '35vh' }}
              >
                <Graph data={graphIndex} />
              </Carousel>
            </Grid>
          </Paper>
        </Grid>
        <Grid item xs={6} container className={classes.GridNews}>
          <Paper elevation={1} className={classes.paper}>
            <Box display="flex" flexDirection="column" >
              <Typography variant="h1" id="homeNews" className={classes.newsHeading} noWrap>
                Latest News
              </Typography>
              <LatestNews />
            </Box>
          </Paper>
        </Grid>
      </Grid>
      <Dialog
        fullWidth={true}
        maxWidth={'lg'}
        onClose={() => {
          setBot(false);
        }}
        aria-labelledby="customized-dialog-title"
        open={bot}
      >
        <DialogTitle className={classes.ModalTitle}>
          <Grid container>
            <Grid
              container
              item
              xs={12}
              justify="flex-end"
              alignItems="center"
            >
              <IconButton
                aria-label="delete"
                className={classes.ModalCloseIconButton}
                onClick={() => {
                  setBot(false);
                }}
              >
                <CloseIcon className={classes.ModalCloseIcon} />
              </IconButton>
            </Grid>
          </Grid>
        </DialogTitle>
        <DialogContent className={classes.Dialog}>
          <Bot page={"Core"} />
        </DialogContent>
      </Dialog>
      <BotTooltip
        placement="right-start"
        open={tooltipIsOpen}
        title={
          <React.Fragment>
            <Typography className={classes.botIntroPopup}>
              Hi I'm ARIA
              </Typography>
            <Typography className={classes.botTextPopup}>
              I can help you find information in Approval Documents.
              </Typography>
            <Typography className={classes.botTextPopup}>
              Ask me questions
              </Typography>
          </React.Fragment>
        }
      >
        <Fab
          aria-label="add"
          className={classes.botIconFAB}
          onClick={() => setBot(true)}
        >
          <img className={classes.botIcon} src={BotLogo}></img>
        </Fab>
      </BotTooltip>
      <Fab
        aria-label="add"
        className={classes.airplaneIcon}
        onClick={() => {
          setStepsEnabled(true)

        }}
      >
        <Airplane />
      </Fab>
    </div>
  );
}
