import React, { Fragment, useEffect, useState, useContext } from 'react';
//MATERIAL UI IMPORTS
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
//other components
import MainStyles from '../styles/505b2.styles';
import Cards from './Cards';
import LineGraph from './LineGraph';
import Percentages from './Percentages';
import PlusIcon from '../../../Images/svgs/plus';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';
import Base64 from 'js-base64';
import { get505b2Stats } from '../../ServiceApi/services';

const BusinessIntelligence = ({
  accelerated_approval,
  fastrack,
  breakthrough_designation,
  yearGraph,
  activeIngredients,
  formulations,
  blaCount,
}) => {
  const classes = MainStyles();
  const { state, dispatch } = useContext(Store);
  const [percentages, setPercentages] = useState();
  const [linegraph, setLineGraph] = useState();
  const [cards, setCards] = useState();

  useEffect(() => {
    async function getData() {
      try {
        const response = await get505b2Stats();
        console.log(response.data)
        const percentagesData = {
          acceleratedApproval: accelerated_approval || response.data.Success.accelerated_approval,
          breakthroughDesignation: breakthrough_designation || response.data.Success.breakthrough_designation,
          fasttrack: fastrack || response.data.Success.fasttrack,
        };
        setPercentages(percentagesData);
        let approvaltimeGraphData = [];
        for (let elem of response.data.Success.approval_time) {
          let year = elem.year;
          let count = parseInt(elem.count);
          approvaltimeGraphData.push({ count, year });
        }
        // approvaltimeGraphData.sort(
        //   (a, b) => parseInt(a.year) - parseInt(b.year)
        // );
        let approvalyearGraphData = [];
        for (let elem of yearGraph ||
          response.data.Success.approval_year) {
          let year = elem.year;
          let count = parseInt(elem.count);
          approvalyearGraphData.push({ count, year });
        }
        // approvalyearGraphData.sort(
        //   (a, b) => parseInt(a.year) - parseInt(b.year)
        // );

        const lineGraphData = {
          approvalTimeGraph: approvaltimeGraphData,
          approvalYearGraph: approvalyearGraphData,
        };
        setLineGraph(lineGraphData);
        setCards([
          {
            id: 'Active Ingredients',
            count:
              blaCount || response.data.Success.active_ingredients,
          },
          {
            id: 'Formulations',
            count:
              activeIngredients ||
              response.data.Success.formulations,
          },
          {
            id: 'Routes',
            count:
              formulations ||
              response.data.Success.route,
          },
        ]);
        // });
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, []);

  const addProgressBars = async () => {
    await dispatch({
      type: Actions.SET_CART_ITEMS,
      value: state.cartItems + 1,
    });
    await dispatch({
      type: Actions.SET_REPORT,
      value: { id: 'Progress Bars', title: '505(b)(2)', data: percentages },
    });
  };
  return (
    <Fragment>
      <Grid container>
        <Grid item lg={2}>
          <Cards data={cards} />
        </Grid>
        <Grid item lg={10}>
          <Grid item lg={12}>
            <LineGraph data={linegraph} />
          </Grid>
          <Grid item lg={12}>
            <Box
              mr={2}
              display="flex"
              alignItems="center"
              justifyContent="flex-end"
              className={classes.percentages}
            >
              <Typography onClick={addProgressBars} className={classes.add}>
                <PlusIcon /> Add to report
              </Typography>
            </Box>
            <Percentages data={percentages} />
          </Grid>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default BusinessIntelligence;
