import React, { Fragment } from 'react';
//MATERIAL UI IMPORTS
import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Skeleton from '@material-ui/lab/Skeleton';
//external libs
import AnimatedNumber from "animated-number-react";
//material ui icons
import SummaryIcon from '../../../Images/svgs/summaryIcon';
//styles
import CardsStyles from '../styles/Cards.styles';

const Cards = ({ data }) => {
  const classes = CardsStyles();
  let formatValue = value => `${Number(value).toFixed(0)}`;
  return (
    <Fragment>
      <Box display="flex" flexDirection="column" justifyContent="">
        <Typography align="left" className={classes.headings}>Overview</Typography>
        <Box display="flex" flexDirection="column" alignItems="" className={classes.cards}>
          {data ? (
            data && data.map((item) => {
              return (
                <Paper elevation={2} className={classes.paper}>
                  <Box padding={1} display="flex" flexDirection="column" justifyContent="space-evenly" alignItems="center" className={classes.cardContent}>
                    {/* <IconButton className={classes.paperIcon}>
                      <AddIcon fontSize="small" />
                    </IconButton> */}
                    {/* <SummaryIcon /> */}
                    <Typography className={classes.number}>
                      <AnimatedNumber className="animatedNumber" value={String(item.count)} formatValue={formatValue} duration='1000' />
                      {/* {item.count} */}
                    </Typography>
                    <Typography className={classes.cardText}>
                      {item.id}
                    </Typography>
                  </Box>
                </Paper>
              );
            })
          ) : (
              <Box ml={5} display="flex" flexDirection="column" alignItems="" className={classes.cards}>
                <Skeleton className={classes.paper} />
                <Skeleton className={classes.paper} />
                <Skeleton className={classes.paper} />
              </Box>
            )}
        </Box>
      </Box>
    </Fragment>
  );
}

export default Cards;