import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
//material ui imports
import Button from "@material-ui/core/Button";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Container from "@material-ui/core/Container";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Box from "@material-ui/core/Box";
import Divider from "@material-ui/core/Divider";
//context
import Store from "../../../store";
import Actions from "../../../store";
// other components
import Styles from "../styles/DataEntry.styles";

const DataEntry = () => {
  const classes = Styles();
  const history = useHistory();
  const { dispatch } = useContext(Store);
  const [docType, setDocType] = useState("Letter");
  const [open, setOpen] = useState(false);
  const [applNo, setApplNo] = useState();
  const [subNo, setSubNo] = useState();
  const [subType, setSubType] = useState("SUPPL");
  const [formSubmit, setFormSubmit] = useState(false);
  const [key, setKey] = useState();

  const handleChange = (event) => {
    setDocType(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  const handleApplcationChange = (event) => {
    setApplNo(event.target.value);
  };

  const handleSubmissionTypeChange = (event) => {
    setSubType(event.target.value);
  };

  const handleSubmissionIdChange = (event) => {
    let text = event.target.value;
    setSubNo(text);
  };

  const handleKeyChange = (event) => {
    let text = event.target.value;
    setKey(text);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    history.push(`/admin/${applNo}/${subType}/${subNo}/${docType}`);
    setFormSubmit(true);
  };

  const handleKeyFormSubmit = async (event) => {
    event.preventDefault();
    let validate_input = key.split("-");
    if (validate_input.length != 3) {
      await dispatch({
        type: Actions.SET_ALERT,
        value: {
          status: true,
          message: "Enter a valid hiphenated input. Eg: 004782-SUPPL-171",
        },
      });
    } else {
      let appl_no = key.split("-")[0];
      let sub_type = key.split("-")[1].toUpperCase();
      let sub_no = key.split("-")[2];
      history.push(`/admin/${appl_no}/${sub_type}/${sub_no}/letter`);
      setFormSubmit(true);
    }
  };

  return (
    <Box className={classes.root} display="flex" alignItems="center">
      <Container maxWidth="lg" className={classes.container}>
        <Paper elevation={1} className={classes.banner}>
          <Typography className={classes.formHeadings}>
            Enter the details to review the document
          </Typography>
          <form onSubmit={handleKeyFormSubmit}>
            <Grid container spacing={1}>
              <Grid item xs={12} lg={6}>
                <Box margin={1} display="flex" flexDirection="column">
                  <Typography align="left" className={classes.label}>
                    Application Number
                  </Typography>
                  <TextField
                    required
                    className={classes.textfield}
                    onChange={handleKeyChange}
                    fullWidth
                    id="standard-basic"
                    placeholder="Enter a key. Eg: 004782-ORIG-1"
                    InputProps={{
                      classes: { input: classes.input },
                      disableUnderline: true,
                    }}
                  />
                </Box>
              </Grid>
              <Grid item xs={12} lg={6}>
                <Box
                  margin={1}
                  display="flex"
                  alignItems="center"
                  className={classes.btn}
                >
                  <Button
                    type="submit"
                    variant="contained"
                    className={classes.button}
                  >
                    Submit
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </form>
          <Box
            display="flex"
            flexDirection="row"
            justifyContent="space-evenly"
            alignItems="center"
            className={classes.dividerRoot}
          >
            <Divider className={classes.divider} />
            <Typography className={classes.or}>OR</Typography>
            <Divider className={classes.divider} />
          </Box>
          <form onSubmit={handleSubmit}>
            <Grid container spacing={4}>
              <Grid item xs={12} lg={4}>
                <Box margin={1} flexDirection="column">
                  <Typography align="left" className={classes.label}>
                    Application Number
                  </Typography>
                  <TextField
                    required
                    className={classes.textfield}
                    onChange={handleApplcationChange}
                    fullWidth
                    id="standard-basic"
                    placeholder="Enter an application number"
                    InputProps={{
                      classes: { input: classes.input },
                      disableUnderline: true,
                    }}
                  />
                </Box>
              </Grid>
              <Grid item xs={12} lg={4}>
                <Box margin={1} flexDirection="column">
                  <Typography align="left" className={classes.label}>
                    Submission Id
                  </Typography>
                  <Box
                    display="flex"
                    flexDirection="row"
                    justifyContent="space-evenly"
                  >
                    <RadioGroup
                      row
                      aria-label="submissionType"
                      name="submissionType"
                      value={subType}
                      onChange={handleSubmissionTypeChange}
                    >
                      <FormControlLabel
                        value="SUPPL"
                        className={classes.radioLabel}
                        control={
                          <Radio color="default" className={classes.radio} />
                        }
                        label="SUPPL"
                        labelPlacement="end"
                      />
                      <FormControlLabel
                        value="ORIG"
                        className={classes.radioLabel}
                        control={
                          <Radio color="default" className={classes.radio} />
                        }
                        label="ORIG"
                        labelPlacement="end"
                      />
                    </RadioGroup>
                    <TextField
                      onChange={handleSubmissionIdChange}
                      type="number"
                      required
                      className={classes.textfield}
                      placeholder="Enter number"
                      InputProps={{
                        classes: { input: classes.input },
                        disableUnderline: true,
                      }}
                    />
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12} lg={4}>
                <Box margin={1} flexDirection="column">
                  <Typography align="left" className={classes.label}>
                    Select a document type
                  </Typography>
                  <Select
                    id="blaStats"
                    variant="standard"
                    disableUnderline
                    required
                    MenuProps={{
                      anchorOrigin: {
                        vertical: "bottom",
                        horizontal: "left",
                      },
                      transformOrigin: {
                        vertical: "top",
                        horizontal: "left",
                      },
                      getContentAnchorEl: null,
                    }}
                    open={open}
                    onClose={handleClose}
                    onOpen={handleOpen}
                    value={docType}
                    onChange={handleChange}
                    className={classes.dropdownContainer}
                    inputProps={{
                      classes: {
                        icon: classes.icon,
                      },
                    }}
                  >
                    <MenuItem value="Letter" className={classes.dropdownText}>
                      Letter
                    </MenuItem>
                    <MenuItem
                      value=""
                      className={classes.dropdownText}
                    ></MenuItem>
                    {/* <MenuItem value={30}></MenuItem> */}
                  </Select>
                </Box>
              </Grid>
            </Grid>
            <Box padding={2} display="flex" justifyContent="center">
              <Button
                type="submit"
                variant="contained"
                className={classes.button}
              >
                Submit
              </Button>
            </Box>
          </form>
        </Paper>
      </Container>
    </Box>
  );
};

export default DataEntry;
