import React, { useState, useEffect, useContext, Fragment } from 'react';
//MATERIAL UI IMPORTS
import Box from '@material-ui/core/Box';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Fab from '@material-ui/core/Fab';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import BotLogo from '../../Images/bot.svg';
import Bot from '../Bot';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';

//styles
import MainStyles from './styles/505b2.styles';
//other components
import BusinessIntelligence from './BusinessIntelligence';
//store
import Store from '../../store';
import Actions from '../../store/actions';
//guide tour
import { Steps } from 'intro.js-react';
import 'intro.js/introjs.css';
import Airplane from '../../Images/svgs/airplane';

const BotTooltip = withStyles((theme) => ({
  tooltip: {
    marginTop: '-11vh',
    marginLeft: '-1vh',
    width: 'fit-content',
    padding: '10px',
    paddingLeft: '20px',
    paddingRight: '20px',
    borderRadius: '10px',
    color: theme.palette.white,
    border: `2px solid ${theme.palette.primary.lightBorder}`,
  },
}))(Tooltip);

const ApprovalPathway = () => {
  const classes = MainStyles();
  const { state, dispatch } = useContext(Store);
  const [dashboard, setDashboard] = useState('Business Intelligence');
  const [open, setOpen] = React.useState(false);
  const [bot, setBot] = useState(false);
  const [tooltipIsOpen, setTooltipIsOpen] = useState(true);
  //guide tour state
  const [steps, setSteps] = useState([
    {
      element: '#approvalPathwayStats',
      intro:
        'Business intelligence view provides graphs by number of approvals by year, approval time for original application as well as proportion approved by accelerated approval, breakthrough designation, Fast Track and Orphan designation',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    }
  ]);
  const [stepsEnabled, setStepsEnabled] = useState(false);

  const onExit = () => {
    setStepsEnabled(false);
  };

  useEffect(() => {
    async function getData() {
      try {
        await dispatch({ type: Actions.SET_MODULE, value: '505b2' })
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, [dispatch]);

  const handleChange = (event) => {
    setDashboard(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <Box className={classes.root}>
      <Steps
        enabled={stepsEnabled}
        steps={steps}
        showProgress={true}
        scrollToElement={true}
        // ref={steps => (this.steps = steps)}
        initialStep={0}
        // onBeforeChange={onBeforeChange}
        onExit={onExit}
      />
      <Box display="flex" justifyContent="flex-start" alignItems="center" className={classes.dropdownBox}>
        <FormControl className={classes.formControl}>
          <Select
            labelId="demo-controlled-open-select-label"
            id="approvalPathwayStats"
            variant="standard"
            disableUnderline
            MenuProps={{
              anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
              },
              transformOrigin: {
                vertical: "top",
                horizontal: "left"
              },
              getContentAnchorEl: null
            }}
            open={open}
            onClose={handleClose}
            onOpen={handleOpen}
            value={dashboard}
            onChange={handleChange}
            className={classes.dropdownContainer}
          >
            <MenuItem value="Business Intelligence" className={classes.dropdownText}>Business Intelligence</MenuItem>
            <MenuItem value={30}></MenuItem>
          </Select>
        </FormControl>
      </Box>
      {dashboard === "Business Intelligence" ? (
        <BusinessIntelligence />
      ) : null}
      <Dialog
        fullWidth={true}
        maxWidth={'lg'}
        onClose={() => {
          setBot(false);
        }}
        aria-labelledby="customized-dialog-title"
        open={bot}
      >
        <DialogTitle className={classes.ModalTitle}>
          <Grid container>
            <Grid
              container
              item
              xs={12}
              justify="flex-end"
              alignItems="center"
            >
              <IconButton
                aria-label="delete"
                className={classes.ModalCloseIconButton}
                onClick={() => {
                  setBot(false);
                }}
              >
                <CloseIcon className={classes.ModalCloseIcon} />
              </IconButton>
            </Grid>
          </Grid>
        </DialogTitle>
        <DialogContent className={classes.Dialog}>
          <Bot page={"505b2"} />
        </DialogContent>
      </Dialog>
      <BotTooltip
        placement="right-start"
        open={tooltipIsOpen}
        title={
          <React.Fragment>
            <Typography className={classes.botIntroPopup}>
              Hi I'm ARIA
              </Typography>
            <Typography className={classes.botTextPopup}>
              I can help you find information in Approval Documents.
              </Typography>
            <Typography className={classes.botTextPopup}>
              Ask me questions
              </Typography>
          </React.Fragment>
        }
      >
        <Fab
          aria-label="add"
          className={classes.botIconFAB}
          onClick={() => setBot(true)}
        >
          <img className={classes.botIcon} src={BotLogo}></img>
        </Fab>
      </BotTooltip>
      <Fab
        aria-label="add"
        className={classes.airplaneIcon}
        onClick={() => {
          window.scroll(0, 0);
          setStepsEnabled(true)

        }}
      >
        <Airplane />
      </Fab>
    </Box>
  );
}

export default ApprovalPathway;