import React, { Fragment, useState, useEffect, useContext } from 'react';
//MATERIAL UI IMPORTS
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
//other components
import PlusIcon from '../../../Images/svgs/plus';
import ImmunogenicityStyles from '../styles/Biologics.styles';
import ProgressGraph from './ProgressGraph';
import BarGraph from './BarGraph';
import PieCharts from './PieCharts';
//store
import Store from '../../../store';
import Actions from '../../../store/actions';
import Base64 from 'js-base64';
import { getBLAstats } from '../../ServiceApi/services';

const Immunogenicity = ({ overviewGraph, reportingGraph, yearGraph }) => {
  const classes = ImmunogenicityStyles();
  const { state, dispatch } = useContext(Store);
  const [progressGraph, setProgressGraph] = useState();
  const [barGraph, setBarGraph] = useState();
  const [pieCharts, setPieCharts] = useState();

  useEffect(() => {
    async function getData() {
      try {
        console.log('Immun enter');
        console.log(overviewGraph);
        console.log(reportingGraph);
        console.log(yearGraph);
        var Base64 = require('js-base64').Base64;
        const tok = 'developers:>;JQwa[zL!EX+f{?3{fjnK';
        const hash = Base64.encode(tok);
        const Basic = 'Basic ' + hash;

        // await fetch(
        //   // 'https://ria-staging.vivpro.org:8443/v2/clignosis/bla_stats',
        //   // 'https://ria-api.vivpro.ai:8443/v2/clignosis/bla_stats',
        //   'https://ria-api-staging.vivpro.ai:8443/v2/clignosis/bla_stats',
        //   { headers: { Authorization: Basic } }
        // )
        let response;
        await getBLAstats()
          .then((res) => {
            response = res;
          })
          .catch((error) => {
            console.log(error);
          });
        // .then((response) => response.json())
        // .then((data) => {
        const progressData =
          overviewGraph || response.data.Success.Immunogenicity.overviewGraph;
        const colors = [
          '#0E5A94',
          '#318AC8',
          '#008B8B',
          '#3AB09E',
          '#48E0A6',
          '#1C2D59',
          '#20A1C7',
        ];
        for (let [index, elem] of progressData.entries()) {
          elem.color = colors[index];
        }
        // console.log(progressData);
        setProgressGraph(progressData);
        const barData =
          yearGraph || response.data.Success.Immunogenicity.yearGraph;
        barData.sort((a, b) => parseInt(a.year) - parseInt(b.year));
        setBarGraph(barData);
        // console.log(barData);
        const pieData =
          reportingGraph || response.data.Success.Immunogenicity.reportingGraph;
        setPieCharts(pieData);
        // console.log(pieData)
        // });
        // console.log(response.json());
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, []);

  const addProgressGraph = async () => {
    await dispatch({
      type: Actions.SET_CART_ITEMS,
      value: state.cartItems + 1,
    });
    await dispatch({
      type: Actions.SET_REPORT,
      value: { id: 'Progress Graph', data: progressGraph },
    });
  };

  return (
    <Fragment>
      <Grid container>
        <Grid item lg={6}>
          <Box
            mr={2}
            display="flex"
            alignItems="center"
            justifyContent="space-between"
          >
            <Typography align="left" className={classes.headings}>
              Overview
            </Typography>
            <Typography onClick={addProgressGraph} className={classes.add}>
              <PlusIcon /> Add to report
            </Typography>
          </Box>
          <ProgressGraph data={progressGraph} />
        </Grid>
        <Grid item lg={6}>
          <BarGraph data={barGraph} />
        </Grid>
        <Grid item lg={12}>
          {pieCharts && pieCharts.length !== 0 ? (
            <PieCharts data={pieCharts} />
          ) : null}
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default Immunogenicity;
