import React, { useEffect, useState } from 'react';
import Table from '@material-ui/core/Table';
import Typography from '@material-ui/core/Typography';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Pagination from '@material-ui/lab/Pagination';
import Link from '@material-ui/core/Link';
import Button from '@material-ui/core/Button';
import TableBoxStyles from './TableBox.styles';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '../../../Images/svgs/searchIcon';
import { CustomLongTextfield } from '../../CustomComponents/Textfield';
// import data from './data.json'
import { CustomSearchDropdown } from '../../CustomComponents/Dropdowns';
import { CSVLink, CSVDownload } from 'react-csv';
import download from '../../../Images/download-button.svg';
import TableSortLabel from '@material-ui/core/TableSortLabel';

import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';

const axios = require('axios');

const icon = <CheckBoxOutlineBlankIcon fontSize="small" />;
const checkedIcon = <CheckBoxIcon fontSize="small" />;

export default function TableBox(props) {
  const [pageCount, setPageCount] = useState();
  const [pageNumber, setPageNumber] = useState(1);
  const [pageRows, setPageRows] = useState(props.data);
  const [keywords, setKeywords] = useState(props.ColNames);
  const [search, setSearch] = useState();
  const [filter, setFilter] = useState('Commitment Description');
  const [allRows, setAllRows] = useState([]);
  const [rows, setRows] = useState([]);
  const [columnItems, setColumnItems] = useState();
  const [loading, setLoading] = useState(true);
  const [flag, setFlag] = useState(true);
  const [HideShow, setHideShow] = useState(false, {});
  const [filenameD, setfilenameD] = useState();

  const [createTableHeader, setcreateTableHeader] = useState([
    {
      columnKey: 'CMT_ID',
      columnName: 'Commitment ID',
      isChecked: true,
    },
    {
      columnKey: 'CMT_NUMBER',
      columnName: 'Commitment Number',
      isChecked: true,
    },
    {
      columnKey: 'CMT_DOC_TYPE',
      columnName: 'Application Type',
      isChecked: true,
    },
    {
      columnKey: 'NDA_NUMBER',
      columnName: 'Application Number',
      isChecked: true,
    },
    {
      columnKey: 'CMT_DOC_TYPE_NO',
      columnName: 'Submission Number',
      isChecked: true,
    },
    {
      columnKey: 'PRODUCT',
      columnName: 'Product',
      isChecked: true,
    },

    {
      columnKey: 'NDA_BLA_APPROVAL_DATE',
      columnName: 'Approval Date',
      isChecked: true,
    },
    {
      columnKey: 'CMT_DESC',
      columnName: 'Commitment Description',
      isChecked: true,
    },
    {
      columnKey: 'CMT_STATUS',
      columnName: 'Commitment Status',
      isChecked: true,
    },
    {
      columnKey: 'SUBPART_FLAG',
      columnName: 'Subpart Flag',
      isChecked: true,
    },
    {
      columnKey: 'NCT1',
      columnName: 'NCT ID',
      isChecked: true,
    },
    {
      columnKey: 'LastCTUpdate1',
      columnName: 'Last Update Date',
      isChecked: true,
    },
    {
      columnKey: 'N1',
      columnName: 'Sample Size',
      isChecked: true,
    },
    {
      columnKey: 'APPLICANT',
      columnName: 'Applicant',
      isChecked: true,
    },
    {
      columnKey: 'CMT_STATUS_DESC',
      columnName: 'Commitment Status Description',
      isChecked: true,
    },
    {
      columnKey: 'STUDY_TYPE',
      columnName: 'Study type',
      isChecked: true,
    },
    {
      columnKey: 'STUDY_START_DATE',
      columnName: 'Study start date',
      isChecked: true,
    },
    {
      columnKey: 'PROTOCOL_SUBMISSION_DATE',
      columnName: 'Protocol Submission Date',
      isChecked: true,
    },
    {
      columnKey: 'FINAL_RPT_RECV_DATE',
      columnName: 'Final Report Received Data',
      isChecked: true,
    },
    {
      columnKey: 'ANNUAL_RPT_DUE_DATE',
      columnName: 'Annual report due date',
      isChecked: true,
    },
    {
      columnKey: 'ANNUAL_RPT_RECV_DATE',
      columnName: 'Annual report rececived date',
      isChecked: true,
    },
    {
      columnKey: 'FINAL_REPORT_DUE_DATE',
      columnName: 'Final report due date',
      isChecked: true,
    },
  ]);
  const [Allselected, setAllselected] = useState(true);

  const classes = TableBoxStyles();

  const handleSearch = async (event) => {
    event.preventDefault();
    if (flag) {
      setFlag(false);
    } else {
      setFlag(true);
    }
  };

  const handleChangeFilter = async (event) => {
    setFilter(event.target.value);
  };

  useEffect(() => {
    async function getData() {

      // const response = await axios.get(
      //   `https://ria-api-staging.vivpro.ai:8443/v2/PMR/impact`,
      //   { headers: { Authorization: Basic } }
      // );

      // console.log(response)

      // setDataRows(response)

      const fullData = props.data;
      for (var i = 0; i < fullData.length; i++) {
        let row = {
                    'NDA_NUMBER':fullData[i]['NDA_NUMBER'],
                    // 'CMT_DOC_TYPE':fullData[i]['CMT_DOC_TYPE'],
                    // 'PRODUCT' :fullData[i]['PRODUCT'],
                    // 'PROTOCOL_SUBMISSION_DATE' :fullData[i]['PROTOCOL_SUBMISSION_DATE'],
                    "CMT_STATUS" : fullData[i]['CMT_STATUS'],
                    "SUBPART_FLAG" : fullData[i]['SUBPART_FLAG'],
                    'FINAL_REPORT_DUE_DATE' :fullData[i]['FINAL_REPORT_DUE_DATE'],
                    'CMT_NUMBER' :fullData[i]['CMT_NUMBER'],
                    'CMT_DESC':fullData[i]['CMT_DESC']
        }
        fullData[i] = row
      }
      setAllRows(fullData);
      let day = new Date();
      let d =
        day.getMonth() + 1 + '/' + day.getDate() + '/' + day.getFullYear();
      setfilenameD('ria-pmr-' + d + '.csv');
      let results = [];
      if (search) {
        let currentFilter = Object.keys(keywords).find(
          (key) => keywords[key] === filter
        );
        for (var i = 0; i < fullData.length; i++) {
          let filterWord = String(fullData[i][currentFilter]);
          filterWord = filterWord.toLowerCase();
          if (filterWord.includes(search)) {
            results.push(fullData[i]);
          }
        }
      } else {
        results = fullData;
      }

      setRows(results);
      if (!props.data[0]) {
      } else {
        let cols = Object.keys(props.data[0]);
        for (let i = 0; i < cols.length; i++) {
          cols[i] = keywords[cols[i]];
        }
        setColumnItems(cols);
      }
      if (results.length > 0) {
        if (results.length < 50) {
          setPageCount(1);
        } else if (results.length % 50 === 0) {
          setPageCount(results.length / 50);
        } else {
          setPageCount(~~(results.length / 50) + 1);
        }

        const pageData = results.slice((pageNumber - 1) * 50, pageNumber * 50);
        setPageRows(pageData);
        setRows(results);
      } else {
        if (search && results.length === 0) {
          const pageData = [];
          setPageRows(pageData);
        } else {
          if (fullData.length < 50) {
            setPageCount(1);
          } else if (fullData.length % 50 === 0) {
            setPageCount(fullData.length / 50);
          } else {
            setPageCount(~~(fullData.length / 50) + 1);
          }
          const pageData = fullData.slice(
            (pageNumber - 1) * 50,
            pageNumber * 50
          );
          setPageRows(pageData);
          setRows(fullData);
        }
      }
      setLoading(false);
    }
    getData();
  }, [pageNumber, flag, props.data]);

  const hideShowColumn = (event) => {
    let createTableHeader2 = createTableHeader;
    for (const each of createTableHeader2) {
      if (each.columnKey == event.target.value) {
        each.isChecked = event.target.checked;
      }
    }

    setcreateTableHeader(createTableHeader2);
    let All = createTableHeader.every(function (col) {
      return col.isChecked == true;
    });
    setAllselected(All);
    if(flag) {
      setFlag(false);
    } 
    else {
      setFlag(true);
    }
  };

  if (loading === false && allRows.length > 0) {
    return (
      <div className="complianceDashboard">
        <Box display="flex" justifyContent="flex-start">
          <form onSubmit={handleSearch} className={classes.Form}>
            <Box display="flex" id="pmrSearchBox" justifyContent="flex-start">
              <CustomSearchDropdown
                id={'filter'}
                menuItems={columnItems}
                dropdownValue={filter}
                title={'Column'}
                name={'filter'}
                handleChange={handleChangeFilter}
              />
              <CustomLongTextfield
                placeholder="Search keyword"
                id="searchTerm"
                value={search}
                handleChange={(event) => {
                  setSearch(String(event.target.value).toLowerCase());
                }}
                name="searchTerm"
              />
              <IconButton type="submit" className={classes.Button}>
                <SearchIcon />
              </IconButton>
              <Button
                className={classes.ResetButton}
                onClick={() => {
                  setSearch('');
                  if (flag) {
                    setFlag(false);
                  } else {
                    setFlag(true);
                  }
                }}
              >
                Reset
              </Button>
              {search && pageRows.length === 0 ? (
                <Typography className={classes.ErrorTypography}>
                  No Results, Please press the Reset button
                </Typography>
              ) : null}
            </Box>
          </form>
          <div style={{ padding: '3px 0 0 10px' }}>
            {pageRows ? (
              <CSVLink data={rows} filename={filenameD}>
                <img
                  src={download}
                  style={{ width: '33px', height: '42px' }}
                ></img>
              </CSVLink>
            ) : (
                <div></div>
              )}
          </div>
        </Box>

        <Dialog
          fullWidth={true}
          maxWidth={'sm'}
          onClose={() => {
            setHideShow([false, {}]);
          }}
          aria-labelledby="customized-dialog-title"
          open={HideShow[0] !== undefined ? HideShow[0] : false}
        >
          <DialogTitle>Hide/Show Columns</DialogTitle>
          {createTableHeader ? (
            <DialogContent>
              {createTableHeader.map((data) => {
                return (
                  <div>
                    <div class="col-sm-6 pull-left">{data.columnName}</div>
                    <div class="col-sm-6">
                      <label class="customCheckboxContainer pull-right">
                        <Checkbox
                          label={data.columnName}
                          icon={icon}
                          checkedIcon={checkedIcon}
                          style={{ marginRight: 8 }}
                          value={data.columnKey}
                          checked={data.isChecked}
                          // onChange={(data.isChecked = !data.isChecked)}
                          // onChange={(event, value) => hideShowColumn(event, value)}
                          onChange={hideShowColumn}
                        />
                        <span class="checkmark"></span>
                      </label>
                    </div>
                  </div>
                );
              })}
            </DialogContent>
          ) : (
              <div></div>
            )}
        </Dialog>
        {pageRows.length !== 0 ? (
          <>
            <Paper className={classes.root}>
              <Table className={classes.table} aria-label="caption table">
                <TableHead>
                  <TableRow className={classes.TableHeadRow}>
                    {createTableHeader.map((i) => {
                      {
                        return Object.keys(allRows[0]).map((key, index) => {
                          if (i.columnKey === key && i.isChecked === true) {
                            return (
                              <TableCell
                                className={classes.TableHeadCells}
                                align="left"
                              >
                                <TableSortLabel>
                                  {keywords[key]}
                                  {/* {key} */}
                                </TableSortLabel>
                              </TableCell>
                            );
                          }
                        });
                      }
                    })}
                  </TableRow>
                </TableHead>
                <TableBody>
                  {pageRows.map((row) => (
                    <TableRow className={classes.TableBodyRows}>
                      {createTableHeader.map((i) => {
                        {
                          return Object.keys(row).map((key, index) => {
                            if (i.columnKey === key && i.isChecked === true) {
                              return (
                                <TableCell
                                  className={classes.TableBodyCells}
                                  align="left"
                                >
                                  {key === 'NCT1' ? (
                                    <Box className={classes.TableBodyCellBox}>
                                      <Link
                                        target="_blank"
                                        rel="noopener"
                                        href={
                                          'https://clinicaltrials.gov/ct2/show/' +
                                          row[key]
                                        }
                                      >
                                        <Typography
                                          className={
                                            classes.TableCellTypography
                                          }
                                        >
                                          {row[key]}
                                        </Typography>
                                      </Link>
                                    </Box>
                                  ) : key === 'CMT_DESC' ? (
                                    <Box
                                      className={classes.TableBodyCellBoxBig}
                                    >
                                      <Typography
                                        className={classes.TableCellTypography}
                                      >
                                        {row[key]}
                                      </Typography>
                                    </Box>
                                  ) : (
                                        <Box className={classes.TableBodyCellBox}>
                                          <Typography
                                            className={classes.TableCellTypography}
                                          >
                                            {row[key]}
                                          </Typography>
                                        </Box>
                                      )}
                                </TableCell>
                              );
                            }
                          });
                        }
                      })}
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Paper>
            <div className="pagination">
              <Pagination
                count={pageCount}
                onChange={(event, page) => {
                  setPageNumber(page);
                }}
              />
            </div>
          </>
        ) : null}
      </div>
    );
  } else {
    return null;
  }
}
