import React, { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '../../../Images/svgs/searchIcon';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';
import { CustomLongTextfield } from '../../CustomComponents/Textfield';
import BotStyles from '../Bot.styles';
import BotLogo from '../../../Images/bot.svg';
import BotIntroLogo from '../../../Images/BotIntroLogo.svg';
import BotCard from './BotCard';
import SuggestedAnswer from '../BotRes/SuggestedAnswer';
import DescriptionIcon from '@material-ui/icons/Description';
import DownloadLogo from '../../../Images/downloadDark.svg';
import DownloadButton from '../../../Images/downloadButton.svg';
import { CSVLink, CSVDownload } from 'react-csv';
// import download from '../../Images/download.svg';
import NotLike from '../../../Images/NotLike.svg';
import NotDislike from '../../../Images/NotDislike.svg';
import Like from '../../../Images/Like.svg';
import Dislike from '../../../Images/Dislike.svg';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import Filters from '../Filters';
import Base64 from 'js-base64';
import Tooltip from '@material-ui/core/Tooltip';

export default function Bot(props) {
  const classes = BotStyles();
  const [suggestedAns, setsuggestedAns] = useState(props.suggestedAns);
  const [rows, setRows] = useState(props.rows);
  const [applicationNumber, setApplicationNumber] = useState(
    props.applicationNumber
  );
  useEffect(() => {
    try {
      // console.log('props', props);
      window.scrollTo(0, 0);
      document.getElementById('HEADER').scrollIntoView();
      setsuggestedAns(props.suggestedAns);
      setRows(props.rows);
      setApplicationNumber(props.applicationNumber);
      // ReactDOM.findDOMNode(this).scrollIntoView();
    } catch (e) {}
  }, [props]);
  return (
    <>
      <Grid
        container
        item
        xs={12}
        justify="center"
        alignItems="center"
        id="HEADER"
      >
        <Grid item container xs={12} justify="flex-start" alignItems="center">
          {suggestedAns ? (
            <>
              {suggestedAns.length > 1 ? (
                <Typography className={classes.StyledTypographyBlue}>
                  Suggested Answers
                </Typography>
              ) : suggestedAns.length === 1 ? (
                <Typography className={classes.StyledTypographyBlue}>
                  Suggested Answer
                </Typography>
              ) : null}
            </>
          ) : null}
          {suggestedAns
            ? suggestedAns.map((ans) => {
                return (
                  <SuggestedAnswer suggestedAns={suggestedAns} ans={ans} />
                );
              })
            : null}
        </Grid>

        {rows &&
          rows.map((row, index) => (
            <Grid item xs={4} justify="center" alignItems="center">
              <BotCard data={row} />
            </Grid>
          ))}
        <Grid container item xs={12} justify="center" alignItems="center">
          {rows.length < 100 ? null : applicationNumber &&
            applicationNumber.length > 150 ? (
            <Typography className={classes.DisclaimerTypography}>
              Top 100 results for 150 Applications
            </Typography>
          ) : (
            <Typography className={classes.DisclaimerTypography}>
              Top 100 results
            </Typography>
          )}
        </Grid>
      </Grid>
    </>
  );
}
