import React, { useEffect, useState, useContext } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import ExtendedCardStyles from './ExtendedCard.styles';
import Grid from '@material-ui/core/Grid';
import Tooltip from '@material-ui/core/Tooltip';
import Zoom from '@material-ui/core/Zoom';
import Box from '@material-ui/core/Box';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import clsx from 'clsx';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import Collapse from '@material-ui/core/Collapse';
import ReactSpeedometer from 'react-d3-speedometer';
import Fab from '@material-ui/core/Fab';

import ProductTable from './ProductTable';
import PatentTable from './PatentTable';
import ExclusivityTable from './ExclusivityTable';
import AA from '../../Images/aa.png';
import BT from '../../Images/bt.png';
import FT from '../../Images/ft.png';
import G from '../../Images/G.png';
import { ButtonGroup } from '@material-ui/core';
import LinkOutlinedIcon from '@material-ui/icons/LinkOutlined';
import Airplane from '../../Images/svgs/airplane';
//store
import Store from '../../store';
import Actions from '../../store/actions';
//guide tour
import { Steps, Hints } from 'intro.js-react';
import 'intro.js/introjs.css';

export default function ExtendedCard(props) {
  const classes = ExtendedCardStyles();
  const { state, dispatch } = useContext(Store);
  const history = useHistory();

  const [cardData, setCardData] = useState();
  const [patentExpanded, setPatentExpanded] = useState(false);
  const [exclusivityExpanded, setExclusivityExpanded] = useState(false);
  const [applicationType, setApplicationType] = useState();
  const [approvalPathways, setApprovalPathways] = useState();
  const [tab, setTabValue] = useState(0);
  const [expanded, setExpanded] = useState();
  //guide tour state
  const [steps, setSteps] = useState([
    {
      element: '#adminInfo',
      intro:
        'Administrative information: Accelerated approval, Fast Track, Breakthrough, Generic availability and Time for approval(number of days).',
      position: 'top',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#productInfo',
      intro:
        'Product Information: Formulation, Route, Approval Pathway, Number of Generics and indication and Usage from the latest label.',
      position: 'right',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    },
    {
      element: '#prodDetails',
      intro:
        'Product Details: Reference Listed Drug (RLD), Reference Standard (RS), Therapeutic Equivalents (TE), Strength, Marketing Status, Patent and Exclusivity (Dropdowns)',
      position: 'top',
      tooltipClass: 'myTooltipClass',
      highlightClass: 'myHighlightClass',
    }
  ]);
  const [stepsEnabled, setStepsEnabled] = useState(false);

  const onExit = () => {
    setStepsEnabled(false);
  };
  useEffect(() => {
    async function getData() {
      try {
        console.log(props.data);
        setCardData(props.data);
        let applicationTypes = {
          NDA: '#EA5252',
          BLA: '#70319F',
          ANDA: '#FFC100',
        };
        setApplicationType(applicationTypes);
        let approvalPathway = {
          // "" : '#000000',
          '351': '#9A31CD',
          '351(a)': '#258B21',
          '351(k)': '#02CED1',
          '505(b)': '#F6A000',
          '505(b)(2)': '#F6A000',
        };
        setApprovalPathways(approvalPathway);
      } catch (e) {
        console.log(e);
      }
    }
    getData();
  }, []);

  function a11yProps(index) {
    return {
      id: `full-width-tab-${index}`,
      'aria-controls': `full-width-tabpanel-${index}`,
    };
  }

  const handleTabChangeIndex = (index) => {
    setTabValue(index);
  };

  const handleGenericTableClick = (applicationType, applicationNumber) => {
    history.push(`/table/generics/${applicationType}/${applicationNumber}`);
    // history.push({
    //   pathname: '/genericTable',
    //   applicationType: applicationType,
    //   applicationNumber: applicationNumber,
    // });
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  if (cardData) {
    return (
      <div>

        <Grid container>
          <Steps
            enabled={stepsEnabled}
            steps={steps}
            showProgress={true}
            scrollToElement={true}
            // ref={steps => (this.steps = steps)}
            initialStep={0}
            onExit={onExit}
          />
          <Grid xs={12} container justify="center" alignItems="flex-start">
            <Grid xs={3} id="adminInfo" alignItems="flex-start">
              <Grid xs={12} container justify="center" alignItems="center">
                <Grid
                  container
                  justify="flex-start"
                  alignItems="center"
                  item
                  xs={3}
                >
                  <Tooltip
                    TransitionComponent={Zoom}
                    title={
                      'Accelerated Approval: ' +
                      cardData['Accelerated_approval']
                    }
                  >
                    {cardData['Accelerated_approval'] === 'Yes' ? (
                      <Avatar
                        alt="Logo"
                        src={AA}
                        className={classes.ExtendedCardTopLogosYes}
                      />
                    ) : (
                        <Avatar
                          alt="Logo"
                          src={AA}
                          className={classes.ExtendedCardTopLogosNo}
                        />
                      )}
                  </Tooltip>
                </Grid>
                <Grid
                  container
                  justify="flex-start"
                  alignItems="center"
                  item
                  xs={3}
                >
                  <Tooltip
                    TransitionComponent={Zoom}
                    title={'Fast Track: ' + cardData['Fasttrack']}
                  >
                    {cardData['Fasttrack'] === 'Yes' ? (
                      <Avatar
                        alt="Logo"
                        src={FT}
                        className={classes.ExtendedCardTopLogosYes}
                      />
                    ) : (
                        <Avatar
                          alt="Logo"
                          src={FT}
                          className={classes.ExtendedCardTopLogosNo}
                        />
                      )}
                  </Tooltip>
                </Grid>
                <Grid
                  container
                  justify="flex-start"
                  alignItems="center"
                  item
                  xs={3}
                >
                  <Tooltip
                    TransitionComponent={Zoom}
                    title={'Breakthrough: ' + cardData['Breakthrough']}
                  >
                    {cardData['Breakthrough'] === 'Yes' ? (
                      <Avatar
                        alt="Logo"
                        src={BT}
                        className={classes.ExtendedCardTopLogosYes}
                      />
                    ) : (
                        <Avatar
                          alt="Logo"
                          src={BT}
                          className={classes.ExtendedCardTopLogosNo}
                        />
                      )}
                  </Tooltip>
                </Grid>
                <Grid
                  container
                  justify="flex-start"
                  alignItems="center"
                  item
                  xs={3}
                >
                  <Tooltip
                    TransitionComponent={Zoom}
                    title={'Generics: ' + cardData['Generics']}
                  >
                    {cardData['Generics'] === 'Yes' ? (
                      <Avatar
                        alt="Logo"
                        src={G}
                        className={classes.ExtendedCardTopLogosYes}
                      />
                    ) : (
                        <Avatar
                          alt="Logo"
                          src={G}
                          className={classes.ExtendedCardTopLogosNo}
                        />
                      )}
                  </Tooltip>
                </Grid>
              </Grid>
              <Grid xs={12} container justify="center" alignItems="center">
                <h5>Time for Approval (No. of days)</h5>
                {cardData['time_to_approval'] &&
                  cardData['time_to_approval'] != 'N/A' &&
                  cardData['time_to_approval'] != 'NA' ? (
                    <ReactSpeedometer
                      maxValue={
                        cardData['time_to_approval'] > 1095
                          ? cardData['time_to_approval']
                          : 1095
                      }
                      value={cardData['time_to_approval']}
                      width={200}
                      height={150}
                      margin={{
                        top: 15,
                        right: 25,
                        left: 5,
                        bottom: 5,
                      }}
                      needleColor="#000000"
                      customSegmentStops={[
                        0,
                        365,
                        730,
                        cardData['time_to_approval'] > 1095
                          ? cardData['time_to_approval']
                          : 1095,
                      ]}
                      segmentColors={['#66ff66', ' #ffff66', '	#FF4500']}
                    />
                  ) : (
                    <div style={{ width: 200, height: 150 }}>
                      Capture in Progress
                    </div>
                  )}
              </Grid>

              <Grid xs={12} container justify="center" alignItems="center">
                {cardData['tradeName'].length > 9 ? (
                  <Tooltip
                    TransitionComponent={Zoom}
                    title={cardData['tradeName']}
                  >
                    <Typography className={classes.ExtendedCardBrandName}>
                      {cardData['tradeName'].substring(0, 9)}...
                    </Typography>
                  </Tooltip>
                ) : (
                    <Typography className={classes.ExtendedCardBrandName}>
                      {cardData['tradeName']}
                    </Typography>
                  )}
              </Grid>
            </Grid>
            <Grid xs={9} container justify="center" alignItems="center">
              <Box id="productInfo">
                <Grid
                  xs={12}
                  container
                  justify="center"
                  alignItems="center"
                  className={classes.ExtendedCardLine1}
                >
                  <Typography className={classes.ExtendedCardTitle}>
                    {cardData['activeIngredients']}
                  </Typography>
                </Grid>

                <Grid
                  xs={12}
                  container
                  justify="flex-start"
                  alignItems="center"
                  className={classes.ExtendedCardLine2}
                >
                  <Grid
                    container
                    justify="flex-start"
                    alignItems="center"
                    item
                    xs={3}
                  >
                    <Typography className={classes.ExtendedCardFormulation}>
                      {cardData['formulation']}
                    </Typography>
                  </Grid>
                  <Grid
                    container
                    justify="flex-start"
                    alignItems="center"
                    item
                    xs={3}
                  >
                    <Typography className={classes.ExtendedCardFormulation}>
                      {cardData['route']}
                    </Typography>
                  </Grid>
                  <Grid
                    container
                    justify="flex-start"
                    alignItems="center"
                    item
                    xs={3}
                  >
                    <Typography
                      className={classes.ExtendedCardApprovalPathway}
                      style={{
                        backgroundColor:
                          approvalPathways[cardData['approvalPathway']],
                      }}
                    >
                      {cardData['approvalPathway']}
                    </Typography>
                  </Grid>
                  <Grid
                    container
                    justify="flex-start"
                    alignItems="center"
                    item
                    xs={3}
                  >
                    {cardData['generic_count'] === '0' ||
                      cardData['generic_count'] === '' ? (
                        <Typography className={classes.ExtendedCardGenericCount}>
                          Generics: 0
                        </Typography>
                      ) : (
                        <Typography
                          className={classes.ExtendedCardGenericCountClickable}
                          onClick={() => {
                            handleGenericTableClick(
                              cardData['appl_type'],
                              cardData['applno']
                            );
                          }}
                        >
                          {/* <i class="fas fa-link"></i>  */}
                          <LinkOutlinedIcon fontSize="small" />
                      Generics:{' '}
                          {cardData['generic_count']}
                        </Typography>
                      )}
                  </Grid>
                </Grid>
                <Grid
                  xs={12}
                  container
                  justify="center"
                  alignItems="center"
                  className={classes.ExtendedCardLine3}
                >
                  <Typography className={classes.ExtendedCardLabelingTitle}>
                    Labeling( Indication ) :
                </Typography>
                  <Typography className={classes.ExtendedCardLabeling}>
                    {cardData['indications']}
                  </Typography>
                </Grid>
              </Box>
            </Grid>
          </Grid>
          <Grid xs={12} container justify="center" alignItems="center">
            <Grid xs={12} container justify="center" alignItems="center"></Grid>
            <Grid
              xs={12}
              container
              id="prodDetails"
              justify="center"
              alignItems="center"
              className={classes.ExtendedCardProductsGrid}
            >
              <ProductTable products={cardData['products']} />
            </Grid>
            <Grid xs={12} container justify="center" alignItems="center">
              <Button
                onClick={() => {
                  if (patentExpanded) setPatentExpanded(false);
                  else setPatentExpanded(true);
                }}
                className={classes.ExtendedCardDropdownButton}
                aria-expanded={patentExpanded}
                aria-label="show more"
              >
                Patents
                <ExpandMoreIcon
                  className={clsx(classes.expand, {
                    [classes.expandClose]: patentExpanded,
                  })}
                />
              </Button>
            </Grid>
            <Grid
              xs={12}
              container
              justify="center"
              alignItems="center"
              className={classes.ExtendedCardPatentsGrid}
            >
              <Collapse
                className={classes.ExtendedCardDropdown}
                in={state.patentExpansion === true ? true : patentExpanded}
                timeout="auto"
                unmountOnExit
              >
                <PatentTable
                  products={cardData['products']}
                  patent={cardData['patent']}
                />
              </Collapse>
            </Grid>
            <Grid xs={12} container justify="center" alignItems="center">
              <Button
                onClick={() => {
                  if (exclusivityExpanded) setExclusivityExpanded(false);
                  else setExclusivityExpanded(true);
                }}
                className={classes.ExtendedCardDropdownButton}
                aria-expanded={exclusivityExpanded}
                aria-label="show more"
              >
                Exclusivity
                <ExpandMoreIcon
                  className={clsx(classes.expand, {
                    [classes.expandClose]: exclusivityExpanded,
                  })}
                />
              </Button>
            </Grid>
            <Grid
              xs={12}
              container
              justify="center"
              alignItems="center"
              className={classes.ExtendedCardExclusivityGrid}
            >
              <Collapse
                className={classes.ExtendedCardDropdown}
                in={exclusivityExpanded}
                timeout="auto"
                unmountOnExit
              >
                <ExclusivityTable
                  products={cardData['products']}
                  exclusivity={cardData['exclusivity']}
                />
              </Collapse>
            </Grid>
          </Grid>
        </Grid>
        <Fab
          aria-label="add"
          className={classes.airplaneIcon}
          onClick={() => {
            window.scroll(0, 0);
            setStepsEnabled(true)

          }}
        >
          <Airplane />
        </Fab>
      </div>
    );
  } else {
    return <div></div>;
  }
}
